import { createSelector } from 'reselect';
import * as R from 'ramda';
import { rules } from '@sis-cc/dotstatsuite-components';
import { getData, getLayout, getDimensions, getDataLimited, getDataLimitedCount } from './sdmx';
import { getTimeDimensionInverted, getLayout as getVisLayout, getDimensionFormat } from './vis';
import { withIndex } from '../lib/utils';
import * as Layout from '../lib/layout';
import * as Sdmx from '../lib/sdmx';

export const getDataDimensions = () =>
  createSelector(
    getData,
    R.pipe(
      R.pathOr([], ['structure', 'dimensions', 'observation']),
      R.addIndex(R.map)((dimension, index) =>
        R.pipe(
          R.assoc('index', index),
          R.set(R.lensProp('values'), withIndex(R.propOr([], 'values')(dimension))),
        )(dimension),
      ),
    ),
  );

export const getVisDataDimensions = () =>
  createSelector(
    getDataDimensions(),
    R.pipe(
      R.partition(R.pipe(R.propOr([], 'values'), R.length, R.flip(R.gt)(1))),
      R.converge(
        (many, one) => ({
          many: R.indexBy(R.prop('id'))(many),
          one: R.indexBy(R.prop('id'))(one),
        }),
        [R.head, R.last],
      ),
    ),
  );

export const getTableConfigDimensions = createSelector(
  getVisDataDimensions(),
  getDimensionFormat,
  getTimeDimensionInverted,
  ({ many, one }, format, timeDimensionInverted) =>
    R.mergeRight(
      Layout.oneFormat(format)(one),
      Layout.manyFormat(format, timeDimensionInverted)(many),
    ),
);

export const getVisTableData = () =>
  createSelector(getTimeDimensionInverted, getData, R.uncurryN(2, rules.getTableData));
export const getVisTableDataLimited = () =>
  createSelector(getTimeDimensionInverted, getDataLimited, R.uncurryN(2, rules.getTableData));

export const getLimited = () => {
  return getDataLimitedCount;
};
//data.data.dataSets[0].limitedTo=keysLimit;

export const getFlatDimensionsIds = () =>
  createSelector(getDimensions, getDataDimensions(), (structureDimensions, dataDimensions) =>
    R.useWith(Sdmx.getUniq, [Sdmx.getDimensionsIds, Sdmx.getDimensionsIds])(
      structureDimensions,
      dataDimensions,
    ),
  );

export const getVisTableConfigLayout = () =>
  createSelector(
    getVisLayout,
    getLayout,
    getFlatDimensionsIds(),
    getVisDataDimensions(),
    (visLayout = {}, sdmxLayout = {}, dimensionsIds, { many, one }) => {
      const layout = R.pipe(
        R.ifElse(R.isEmpty, R.always(sdmxLayout), R.identity),
        R.mergeRight({ sections: [], rows: [], header: [] }),
      )(visLayout);
      const getMissingIds = R.difference(dimensionsIds);
      const oneIds = R.keys(one);
      const layoutIds = Layout.getValuesFlat(layout);

      if (Layout.isInvalid(dimensionsIds, layoutIds, layout))
        return Layout.getDefaultLayout(many, getMissingIds);

      const missingIds = getMissingIds(layoutIds);
      if (R.isEmpty(missingIds)) return Layout.adjustment(oneIds, layout);

      return Layout.getLayout(missingIds, oneIds)(layout);
    },
  );

export const getVisTableLayout = () =>
  createSelector(getVisTableConfigLayout(), getVisDataDimensions(), (layout, { many }) => {
    const idsNotInMany = R.difference(Layout.getValuesFlat(layout), R.keys(many));
    return R.map(R.flip(R.difference)(idsNotInMany))(layout);
  });
