import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';

//import MapSubfilter from './MapSubfilter';
import MapSubfilterSummary from './MapSubfilterSummary';

class MapFilterGroup extends React.Component {
  render() {
    if (!this.props.filters) return null;
    return (
      <div className="mapFiltersGroup">
        {this.props.filters.map(item => (
          <MapSubfilterSummary
            key={item.id}
            item={item}
            selected={this.props.selection[item.id]}
            onValueChange={this.props.onValueChange}
          />
        ))}
      </div>
    );
  }
}

MapFilterGroup.propTypes = {
  filters: PropTypes.array,
  selection: PropTypes.object,
  onValueChange: PropTypes.func,
};

export default MapFilterGroup;
