import React, { useEffect } from 'react';
import PropTypes from 'prop-types';

const TableMessageUnicef = ({ returnedKeyCount, limitedTo, animationMillis }) => {
  const msgBoxId = 'dataLimited_msgBox';

  if (returnedKeyCount > limitedTo) {
    const remMessage = () => {
      document.getElementById('root').removeEventListener('data_loaded', remMessage);
      const tmr = setTimeout(() => {
        const el = document.getElementById(msgBoxId);
        const onTransEnd = () => {
          const el2 = document.getElementById(msgBoxId);
          el2.removeEventListener('transitionend', onTransEnd);
          el2.style.display = 'none';
        };
        if (el) {
          el.addEventListener('transitionend', onTransEnd, false);
          el.classList.add('dataLimited_msgBox_trans');
        }
      }, animationMillis);
    };

    document.getElementById('root').addEventListener('data_loaded', remMessage);
  } else {
    return null;
  }

  return (
    <div className="div_dataLimited" id={msgBoxId} style={{ position: 'relative' }}>
      <p>
        Visualization is limited to {' ' + limitedTo} values to avoid slowing your browser.<br />
        Please further filter your data, or &lt;Download&gt; the entire dataset as a CSV.
      </p>
      <div
        style={{ position: 'absolute', top: '-20px', right: '-20px' }}
        className="pt-tag-remove"
      />
    </div>
  );
};

TableMessageUnicef.propTypes = {
  returnedKeyCount: PropTypes.number,
  limitedTo: PropTypes.number,
  animationMillis: PropTypes.number,
};

export default TableMessageUnicef;
