const getReferencedCodeLabel = (code, refCodelist) => {
  for (let i = 0; i < refCodelist[code.codelistAliasRef].length; i++) {
    if (refCodelist[code.codelistAliasRef][i].id == code.codeID) {
      return refCodelist[code.codelistAliasRef][i].name;
    }
  }
};

const getCodesHierarchy = (codes, refCodelist) => {
  const ret = [];
  codes.forEach(c => {
    let code = {
      id: c.id,
      codeID: c.codeID,
      name: getReferencedCodeLabel(c, refCodelist),
    };

    if (c.hierarchicalCodes) {
      code.codes = getCodesHierarchy(c.hierarchicalCodes, refCodelist);
    }
    ret.push(code);
  });

  return ret;
};

const getHierarchy = (hierarchy, refCodelist) => {
  const ret = {
    name: hierarchy.name,
    hierarchies: [],
  };

  hierarchy.hierarchies.forEach(h => {
    let node = {
      id: h.id,
      codeID: h.codeID,
      name: h.name,
      codes: getCodesHierarchy(h.hierarchicalCodes, refCodelist),
    };
    ret.hierarchies.push(node);
  });
  return ret;
};

const urnToStructureId = urn => {
  const structureID = urn.split('=')[1];
  const ag = structureID.split(':')[0];
  const id = structureID.split(':')[1].split('(')[0];
  const ver = structureID
    .split(':')[1]
    .split('(')[1]
    .split(')')[0];

  return {
    agencyID: ag,
    id: id,
    version: ver,
  };
};

export default hierarchyParser => {
  const ret = {};
  if (!hierarchyParser.data.hierarchicalCodelists) return ret;
  if (hierarchyParser.data.hierarchicalCodelists.length == 0) return ret;

  const refCodelists = {};
  const includedCL = hierarchyParser.data.hierarchicalCodelists[0].includedCodelists;
  for (var k in includedCL) {
    if (includedCL.hasOwnProperty(k)) {
      const strId = urnToStructureId(includedCL[k]);
      hierarchyParser.data.codelists.forEach(cl => {
        if (cl.agencyID == strId.agencyID && cl.id == strId.id && cl.version == strId.version) {
          refCodelists[k] = cl.codes;
        }
      });
    }
  }

  return getHierarchy(hierarchyParser.data.hierarchicalCodelists[0], refCodelists);
};
