import * as R from 'ramda';
import { getIsSelected, getSortedCodes } from './annotations';

export default structure => {
  const lang = R.pipe(R.pathOr([], ['meta', 'content-languages']), R.head)(structure);

  const dimensions = R.pipe(
    R.pathOr([], ['data', 'dataStructures']),
    R.head,
    R.pathOr([], ['dataStructureComponents', 'dimensionList', 'dimensions']),
  )(structure);
  const conceptSchemes = R.pathOr([], ['data', 'conceptSchemes'])(structure);

  if (R.any(R.anyPass([R.isNil, R.isEmpty]))([lang, dimensions, conceptSchemes])) {
    return { dimensions: [], dataflow: {} };
  }

  const getCodelists = R.pathOr([], ['data', 'codelists']);
  const codelists = getCodelists(structure);

  /*const getLocalisedName = id =>
    R.pipe(R.pathOr('', ['name', lang]), R.ifElse(R.isEmpty, R.always(`[${id}]`), R.identity));*/

  const getLocalisedName = id =>
    R.pipe(R.pathOr('', ['name', lang]), R.ifElse(R.isEmpty, R.always(`[${id}]`), R.identity));

  const getDataflow = R.pipe(
    R.pathOr([], ['data', 'dataflows']),
    R.head,
    R.pick(['id', 'version', 'agencyID', 'name']),
    ({ id, agencyID, ...rest }) => ({
      ...rest,
      code: id,
      agencyId: agencyID,
      name: getLocalisedName(id)(rest),
    }),
  );

  const getDataflowAnnotations = data => {
    return R.pathOr([], ['data', 'dataflows', 0, 'annotations'])(data);
  };

  const filters = R.addIndex(R.reduce)(
    (acc, dimension, index) => {
      const getConceptIdentity = R.propOr(null, 'conceptIdentity');
      if (R.isNil(getConceptIdentity(dimension))) return acc;
      const conceptIdentity = getConceptIdentity(dimension);

      const getUrnToParse = R.match(/[^urn:sdmx:org.sdmx.infomodel][^.|=]+/g);
      if (R.isEmpty(getUrnToParse(conceptIdentity))) return acc;
      const conceptIdentityUrnToParse = getUrnToParse(conceptIdentity);
      const conceptIdentityRepresentationCode = R.last(conceptIdentityUrnToParse);

      const getConceptScheme = R.find(({ agencyID, id, version }) => {
        const getSdmxId = R.match(/=(.*\))/);
        const sdmxId = R.tail(R.head(getSdmxId(conceptIdentity)));
        const sdmxIdRecomposed = `${agencyID}:${id}(${version})`;
        return sdmxIdRecomposed === sdmxId;
      });
      const conceptScheme = getConceptScheme(conceptSchemes);
      const getConcepts = R.propOr(null, 'concepts');
      if (R.isNil(getConcepts(conceptScheme))) return acc;
      const concepts = getConcepts(conceptScheme);

      const getConcept = R.find(R.propEq('id', conceptIdentityRepresentationCode));
      if (R.isNil(getConcept(concepts))) return acc;
      const concept = getConcept(concepts);

      const getId = R.propOr(null, 'id');
      if (R.isNil(getId(dimension))) return acc;
      const id = getId(dimension);

      const getForcedDims = R.propOr(null, 'forced');
      const forced = getForcedDims(dimension);

      // ---------------------------------------------------------------------------------------------
      //                                  values [ ]
      // ---------------------------------------------------------------------------------------------

      const getLocalRepresentation = R.pathOr(null, ['localRepresentation', 'enumeration']);
      if (R.isNil(getLocalRepresentation(dimension))) return [];

      const localRepresentation = getLocalRepresentation(dimension);

      const type = R.nth(1)(getUrnToParse(localRepresentation));

      const getLink = annotations => {
        if (R.isNil(annotations)) return null;
        for (let i = 0; i < annotations.length; i++) {
          if (annotations[i].type == 'link') {
            return annotations[i].value;
          }
        }
        return null;
      };

      const getCodeList = R.find(({ agencyID, id, version }) => {
        const getSdmxId = R.match(/=(.*\))/);
        const sdmxId = R.tail(R.head(getSdmxId(localRepresentation)));
        const sdmxIdRecomposed = `${agencyID}:${id}(${version})`;
        return sdmxIdRecomposed === sdmxId && type === 'Codelist';
      });

      if (R.isEmpty(codelists) || R.isEmpty(R.propOr([], 'codes')(getCodeList(codelists)))) {
        return acc;
      }
      const codes = getSortedCodes(lang)(R.propOr([], 'codes')(getCodeList(codelists)));

      const localisedCodes = index =>
        R.map(({ id, parent, name, annotations = [] }) => ({
          id,
          parentId: parent,
          isDefaultSelected: getIsSelected({ index, id, annotations }),
          //isDefaultSelected: id === 'DSB_STATUS_TOTAL' || id === 'SEX_T',
          label: getLocalisedName(id)({ name }),
          link: getLink(annotations),
        }));

      const parsedDimension = {
        id,
        index,
        roles: !R.isNil(dimension.conceptRoles) ? dimension.conceptRoles : [],
        values: localisedCodes(index)(codes),
        forced,
      };

      return [
        ...acc,
        {
          ...parsedDimension,
          label: getLocalisedName(parsedDimension.id)(concept),
        },
      ];
    },
    [],
    dimensions,
  );
  return {
    dimensions: filters,
    dataflow: getDataflow(structure),
    dataflowAnnotations: getDataflowAnnotations(structure),
  };
};
