import * as R from 'ramda';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { compose, withProps } from 'recompose';
import { getDataLimited } from '../../selectors/sdmx';
import { getLocale, getIsPending } from '../../selectors/app';
import { getDisplay, getPeriod, getDataquery, getDataflow, getViewer } from '../../selectors/vis';
import { getDataUrl } from '../../lib/sdmx';
import { getSdmxAttribute, chartOptions } from '../../lib/settings';
import Main from './Main';

export default compose(
  connect(
    createStructuredSelector({
      isLoadingData: getIsPending('getData'),
      display: getDisplay,
      type: getViewer,
      data: getDataLimited,
      dataflow: getDataflow,
      dataquery: getDataquery,
      locale: getLocale,
      period: getPeriod,
    }),
  ),
  withProps(({ data, dataflow, dataquery, locale, period }) => ({
    dataUrl: getDataUrl({ configArgs: { dataflow, dataquery, locale }, requestArgs: { period } }),
    options: chartOptions,
    formaterIds: {
      decimals: getSdmxAttribute('decimals'),
      prefscale: getSdmxAttribute('prefscale'),
    },
    enhancedData: data,
  })),
)(Main);

