import React from 'react';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { compose, withProps } from 'recompose';
import { FormattedMessage } from 'react-intl';
import { DEVisualisationToolbar } from '@sis-cc/dotstatsuite-ui-components';
import { getIsPending } from '../../selectors/app';
import { getDataflow, getAction, getViewer } from '../../selectors/vis';
import { getMap } from '../../selectors/sdmx';
import { getHelpUrl } from '../../selectors/vis';
import { changeViewer, changeAction } from '../../ducks/vis';
import { changeDataqueryMultipleUpdate } from '../../ducks/vis';
import { getMapAreasToPreselect, getMapRefAreaDimId } from '../../lib/settings';
import { requestDataFile, requestExcelDataFile, requestExcelDataFileLong } from '../../ducks/sdmx';
import menus from './menus';
import actions from './actions';
import * as R from 'ramda';

export default compose(
  connect(
    createStructuredSelector({
      isDownloading: getIsPending('requestingDataFile'),
      dataflow: getDataflow,
      actionId: getAction,
      viewerId: getViewer,
      map: getMap,
      mapAreasToPreselect: getMapAreasToPreselect,
      mapRefAreaDimId: getMapRefAreaDimId,
      helpUrl: getHelpUrl,
    }),
    {
      changeMenu: changeViewer,
      changeActionId: changeAction,
      requestDataFile,
      requestExcelDataFile,
      requestExcelDataFileLong,
      changeViewer,
      changeDataqueryMultipleUpdate,
    },
  ),
  withProps(({ isDownloading, viewerId, actionId, ...rest }) => ({
    loadingActionId: isDownloading ? 'download' : null,
    selectedMenuId: viewerId,
    selectedActionId: actionId,
    actions: actions({ ...rest, viewerId }),
    menus,
    showLabel: <FormattedMessage id="de.visualisation.toolbar.show" />,
    isFilters: R.equals('filters', actionId),
  })),
)(DEVisualisationToolbar);
