import React from 'react';
import PropTypes from 'prop-types';
import { SDMXTable } from '@sis-cc/dotstatsuite-ui-components';
import SisHeader from '@sis-cc/dotstatsuite-ui-header';
//import SisFooter from '@sis-cc/dotstatsuite-ui-footer';
import FooterUnicef from '../FooterUnicef';
import glamorous from 'glamorous';
import Cell from './Cell';
//import Tools from '../Tools';
import Toolbar from '../ToolbarUnicef';
import TableMessageUnicef from '../TableMessageUnicef';
import IndicatorLinkUnicef from '../IndicatorLinkUnicef';

const TableContainer = glamorous.div({
  borderLeftWidth: 0,
  borderRightWidth: 0,
  marginTop: 10, // sync style with chart

  overflowX: 'auto',
});

const Table = ({
  header,
  viewerConfig,
  data,
  layoutIds,
  renderObservation,
  itemFormatter,
  isNotATable,
  footnotes,
  variant,
  limited,
  selItems,
}) => (
  <TableContainer>
    <div className="header_download">
      <SisHeader {...header} fonts={viewerConfig.fonts.header} />
      <Toolbar />
    </div>
    <TableMessageUnicef
      limitedTo={limited.limitedTo}
      returnedKeyCount={limited.returnedKeyCount}
      animationMillis={8000}
    />
    {/* <IndicatorLinkUnicef data={data} selItems={selItems} /> */}
    {isNotATable ? (
      <Cell renderObservation={renderObservation} variant={variant} />
    ) : (
      <div id="tableScrollWrapper1" style={{ transform: 'rotateX(180deg)', overflowX: 'auto' }}>
        <div id="tableScrollWrapper2" style={{ transform: 'rotateX(180deg)' }}>
          <SDMXTable
            data={data}
            layoutIds={layoutIds}
            renderObservation={renderObservation}
            itemFormatter={itemFormatter}
          />
        </div>
      </div>
    )}
    <FooterUnicef label={'UNICEF'} showCopyrightSymbol={true} />
  </TableContainer>
);

Table.propTypes = {
  header: PropTypes.object,
  viewerConfig: PropTypes.object,
  source: PropTypes.object,
  data: PropTypes.object,
  footnotes: PropTypes.object,
  layoutIds: PropTypes.object,
  renderObservation: PropTypes.func,
  itemFormatter: PropTypes.func,
  isNotATable: PropTypes.bool,
  variant: PropTypes.string,
  limited: PropTypes.object,
  selItems: PropTypes.array,
};

export default Table;
