import React from 'react';
import PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';
import { DELayout, DELayoutContent, DESplash } from '@sis-cc/dotstatsuite-ui-components';
import logo from '../assets/data-explorer-logo.png';

class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props);
    this.state = { hasError: false };
  }

  componentDidCatch() {
    this.setState({ hasError: true });
  }

  render() {
    if (!this.state.hasError) return this.props.children;

    if (this.props.isFinal) return <h1>Whoops, something went wrong on our end.</h1>;

    return (
      <DELayout isHome>
        <DELayoutContent>
          <DESplash title={<FormattedMessage id="de.error.title" />} logo={logo} />
        </DELayoutContent>
      </DELayout>
    );
  }
}

ErrorBoundary.propTypes = {
  children: PropTypes.node,
  isFinal: PropTypes.bool,
};

export default ErrorBoundary;
