import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import SisHeader from '@sis-cc/dotstatsuite-ui-header';
import viewerConfig from '../Viewer/config';
import Toolbar from '../ToolbarUnicef';
import Map from './Map';

function MapWrapper(props) {
  return (
    <React.Fragment>
      <div className="header_download">
        <SisHeader title={props.title} fonts={viewerConfig.fonts.header} />
        <Toolbar />
      </div>
      <Map />
    </React.Fragment>
  );
}

const mapStateToProps = state => {
  return {
    title: state.sdmx.data.structure.name,
  };
};

MapWrapper.propTypes = {
  title: PropTypes.string,
};

export default connect(mapStateToProps)(MapWrapper);
