import * as R from 'ramda';
import { updateDataquery, forceMultipleDataqueryUpdate } from '../lib/sdmx';
import { getDimensions } from '../selectors/sdmx';

//-----------------------------------------------------------------------------------------constants
export const CHANGE_PERIOD = '@@vis/sdmx/CHANGE_PERIOD';
export const CHANGE_LASTNOBS = '@@vis/sdmx/CHANGE_LASTNOBS';
export const CHANGE_FILTER = '@@vis/CHANGE_FILTER';
export const CHANGE_VIEWER = '@@vis/CHANGE_VIEWER';
export const CHANGE_ACTION = '@@vis/CHANGE_ACTION';
export const CHANGE_DATAFLOW = '@@vis/sdmx/CHANGE_DATAFLOW';
export const CHANGE_DATAQUERY = '@@vis/sdmx/CHANGE_DATAQUERY';
export const CHANGE_DATAQUERY_ADD_FILTERS = '@@vis/sdmx/CHANGE_DATAQUERY_ADD_FILTERS';
export const OVERRIDE_DATAQUERY = '@@vis/sdmx/OVERRIDE_DATAQUERY';
export const CHANGE_LAYOUT = '@@vis/CHANGE_LAYOUT';
export const CHANGE_DIMENSION_GETTER = '@@vis/CHANGE_DIMENSION_GETTER';
export const CHANGE_IS_TIME_DIMENSION_INVERTED = '@@vis/CHANGE_IS_TIME_DIMENSION_INVERTED';
export const LOAD_DATAFLOWS = '@@vis/sdmx/LOAD_DATAFLOWS';
export const LOAD_HIERARCHY = '@@vis/sdmx/LOAD_HIERARCHY';
export const LOAD_MAP = '@@vis/sdmx/LOAD_MAP';

//------------------------------------------------------------------------------------------creators
export const changeLastnobs = lastnobservations => ({
  type: CHANGE_LASTNOBS,
  payload: { lastnobservations },
});
export const changePeriod = period => ({ type: CHANGE_PERIOD, payload: { period } });
export const changeFilter = filterId => ({ type: CHANGE_FILTER, payload: { filterId } });
export const changeViewer = viewerId => ({ type: CHANGE_VIEWER, payload: { viewerId } });
export const changeAction = actionId => ({ type: CHANGE_ACTION, payload: { actionId } });
export const changeDataflow = dataflow => ({ type: CHANGE_DATAFLOW, payload: { dataflow } });
export const changeLayout = layout => ({ type: CHANGE_LAYOUT, payload: { layout } });
export const changeDimensionGetter = dimensionGetter => ({
  type: CHANGE_DIMENSION_GETTER,
  payload: { dimensionGetter },
});
export const changeIsTimeDimensionInverted = (id, isTimeDimensionInverted) => ({
  type: CHANGE_IS_TIME_DIMENSION_INVERTED,
  payload: { id, isTimeDimensionInverted },
});
export const changeDataquery = (filterId, valueId) => (dispatch, getState) =>
  dispatch({
    type: CHANGE_DATAQUERY,
    payload: { filterId, valueId, dimensions: getDimensions(getState()) },
  });

export const changeDataqueryMultipleUpdate = (filterId, values) => (dispatch, getState) =>
  dispatch({
    type: CHANGE_DATAQUERY_ADD_FILTERS,
    payload: { filterId, values, dimensions: getDimensions(getState()) },
  });

export const loadDataflows = () => ({ type: LOAD_DATAFLOWS, payload: {} });
export const loadHierarchy = (config, dataflow) => ({
  type: LOAD_HIERARCHY,
  payload: { config: config, dataflow: dataflow },
});
export const changeDq = (filterId, valueId, dims) => ({
  type: CHANGE_DATAQUERY,
  payload: { filterId, valueId, dimensions: dims },
});
export const loadMap = mapUrl => ({
  type: LOAD_MAP,
  payload: mapUrl,
});
//-------------------------------------------------------------------------------------------reducer
export default (state = {}, action = {}) => {
  switch (action.type) {
    case CHANGE_ACTION:
      var isFullscreen = R.always(R.equals(action.payload.actionId, 'fullscreen'));
      return R.evolve({
        isFullscreen: R.ifElse(isFullscreen, R.not, R.identity),
        action: R.cond([
          [isFullscreen, R.identity],
          [R.equals(action.payload.actionId), R.always(undefined)],
          [R.T, R.always(action.payload.actionId)],
        ]),
      })(state);
    case CHANGE_FILTER:
      return R.over(
        R.lensProp('filter'),
        filter =>
          R.ifElse(R.equals(filter), R.always(undefined), R.identity)(
            R.path(['payload', 'filterId'], action),
          ),
        state,
      );
    case CHANGE_PERIOD:
      return R.set(R.lensProp('period'), R.path(['payload', 'period'], action), state);
    case CHANGE_LASTNOBS:
      return R.set(
        R.lensProp('lastnobservations'),
        R.path(['payload', 'lastnobservations'], action),
        state,
      );
    case CHANGE_VIEWER:
      return R.set(R.lensProp('viewer'), R.path(['payload', 'viewerId'], action), state);
    case CHANGE_LAYOUT:
      return R.set(R.lensProp('layout'), R.path(['payload', 'layout'], action), state);
    case CHANGE_DIMENSION_GETTER:
      return R.set(
        R.lensProp('dimensionGetter'),
        R.path(['payload', 'dimensionGetter'], action),
        state,
      );
    case CHANGE_IS_TIME_DIMENSION_INVERTED:
      return {
        ...state,
        timeDimensionInverted: R.set(
          R.lensProp(action.payload.id),
          !action.payload.isTimeDimensionInverted,
          state.timeDimensionInverted,
        ),
      };
    case CHANGE_DATAQUERY: {
      //Code added to select/deselect all the descendants of a node when clicked.
      //Codes only have a parent node (no descendants hierarchy)
      /*
      //Get the list of codes for the clicked dimension
      let dimVals = action.payload.dimensions.filter((d) => { return d.id == action.payload.filterId });
      dimVals = dimVals[0];
      //Will contain the list of nodes to select/deselect
      let linkedNodes = [action.payload.valueId];
      //At each step, the list of parents to find the children of
      let parents = [action.payload.valueId];

      //Loop all the nodes, if the node has the clicked one as parent then add it to the parent array
      while (parents.length > 0) {
        parents = dimVals.values.filter((v) => {
          return v.parentId && parents.indexOf(v.parentId) >= 0
        });
        //push the node in the linked node
        parents.forEach((p) => { linkedNodes.push(p.id) });
        //repeat until we cannot find any more descendants
      }

*/
      return R.over(
        R.lensProp('dataquery'),
        updateDataquery(R.path(['payload', 'dimensions'], action))(
          R.path(['payload', 'filterId'], action),
          R.path(['payload', 'valueId'], action),
        ),
        state,
      );
    }
    case CHANGE_DATAQUERY_ADD_FILTERS:
      return R.over(
        R.lensProp('dataquery'),
        forceMultipleDataqueryUpdate(R.path(['payload', 'dimensions'], action))(
          R.path(['payload', 'filterId'], action),
          R.path(['payload', 'values'], action),
        ),
        state,
      );
    case OVERRIDE_DATAQUERY:
      return R.set(R.lensProp('dataquery'), R.prop('dataquery', action), state);
    case CHANGE_DATAFLOW:
      return R.pipe(
        R.set(R.lensProp('viewer'), 'table'),
        R.set(R.lensProp('dataquery'), R.path(['payload', 'dataflow', 'dataquery'], action)),
        R.set(R.lensProp('period'), R.path(['payload', 'dataflow', 'period'], action)),
        R.flip(R.merge)(R.prop('payload', action)),
      )(state);
    case LOAD_MAP:
      return state;

    case LOAD_DATAFLOWS:
      return state;
    case LOAD_HIERARCHY:
      return state;
    default:
      return state;
  }
};
