import { createSelector } from 'reselect';
import * as R from 'ramda';
import { sdmxPeriod } from '../lib/settings';
import { dimensionFormats } from '../lib/utils';

//------------------------------------------------------------------------------------------------#0
const getVis = R.prop('vis');

//------------------------------------------------------------------------------------------------#1
export const getPeriod = createSelector(
  getVis,
  R.pipe(
    R.prop('period'),
    R.ifElse(R.pipe(R.length, R.equals(2)), R.identity, R.always(sdmxPeriod)),
  ),
);

export const getLastnobservations = createSelector(getVis, R.prop('lastnobservations'));

export const getViewer = createSelector(getVis, R.prop('viewer'));

export const getAction = createSelector(getVis, R.prop('action'));

export const getDisplay = createSelector(getVis, R.prop('display'));

export const getFilter = createSelector(getVis, R.prop('filter'));

export const getDataflow = createSelector(getVis, R.prop('dataflow'));

export const getDataquery = createSelector(getVis, R.prop('dataquery'));

export const getIsFullscreen = createSelector(getVis, R.prop('isFullscreen'));

export const getTimeDimensionInverted = createSelector(getVis, R.prop('timeDimensionInverted'));

export const getLayout = createSelector(getVis, R.prop('layout'));

export const getDimensionGetter = createSelector(getVis, R.prop('dimensionGetter'));

export const getDimensionFormat = createSelector(
  getDimensionGetter,
  R.flip(R.prop)(dimensionFormats),
);

export const getHelpUrl = createSelector(getVis, R.prop('helpUrl'));
