export const requestDataflows = () => (dispatch, getState) => {
  const configArgs = {
    locale: getLocale(getState()),
    dataflow: getDataflow(getState()),
  };
  return request(dispatch, { method: 'getDataflows', configArgs }).then(dataflows => {
    dispatch({ type: HANDLE_DATAFLOWS, dataflows });
  });
};

export const requestHierachy = h => (dispatch, getState) => {
  const configArgs = h;
  return request(dispatch, { method: 'getHierarchy', configArgs }).then(hierarchy => {
    dispatch({ type: HANDLE_HIERARCHY, hierarchy });
  });
};

export const requestMap = url => (dispatch, getState) => {
  return request(dispatch, { method: 'getMap', url }).then(map => {
    dispatch({ type: HANDLE_MAP, map });
  });
};

export const change_map_filters = filters => ({
  type: CHANGE_MAP_FILTERS,
  payload: { filters },
});

//-------------------------------------------------------------------------------------------reducer
/*After a filter change, try to keep the old map filters selection
-> user selects sex:F, then adds an indicator to the sdmx data query, try to reselect sex=F
If there is no previous selection or the new selection has different code try to select the total:_T*/

const map_filters_state_helper = (state, action) => {
  if (typeof action.data == 'undefined') return {};

  let ret = {};

  action.data.structure.dimensions.observation.forEach(d => {
    if (state.map_selection.hasOwnProperty(d.id)) {
      let currSel = state.map_selection[d.id];
      if (d.values.findIndex(x => x.id === currSel) != -1) {
        ret[d.id] = currSel;
      } else ret[d.id] = d.values[0].id;
    } else {
      //Assign the fist element and then try to look for a _T
      ret[d.id] = d.values[0].id;
      for (var i = 0; i < d.values.length; i++) {
        if (d.values[i].id == '_T') {
          ret[d.id] = d.values[i].id;
          break;
        }
      }
    }
  });
  return ret;
};
export default (state = {}, action = {}) => {
  let new_filter_map_state = null;
  if (action.type == HANDLE_DATA) new_filter_map_state = map_filters_state_helper(state, action);

  switch (action.type) {
    case HANDLE_STRUCTURE:
      //return R.set(R.lensProp('dimensions'), R.path(['structure', 'dimensions'], action), state);
      return R.pipe(
        R.set(R.lensProp('dimensions'), R.path(['structure', 'dimensions'], action)),
        R.set(
          R.lensProp('dataflowAnnotations'),
          R.path(['structure', 'dataflowAnnotations'], action),
        ),
      )(state);
    case HANDLE_DATA:
      return R.pipe(
        R.set(R.lensProp('data'), R.prop('data', action)),
        R.set(R.lensProp('layout'), R.prop('layout', action)),

        R.set(R.lensProp('map_selection'), new_filter_map_state),
      )(state);
    case HANDLE_DATAFLOWS:
      return R.set(R.lensProp('dataflows'), R.prop('dataflows', action), state);
    case HANDLE_HIERARCHY:
      return R.set(R.lensProp('hierarchy'), R.prop('hierarchy', action), state);
    case HANDLE_MAP:
      return R.set(R.lensProp('map'), R.prop('map', action), state);
    case CHANGE_MAP_FILTERS:
      return {
        ...state,
        map_selection: R.set(
          R.lensProp(action.payload.filter.id),
          action.payload.filter.val,
          state.map_selection,
        ),
      };
    default:
      return state;
  }
};
