import React from 'react';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { compose, withProps, pure } from 'recompose';
import { FormattedMessage } from 'react-intl';
import { DEFilterPeriod } from '@sis-cc/dotstatsuite-ui-components';
import { changePeriod } from '../../ducks/vis';
import { getPeriod } from '../../selectors/vis';
import { sdmxPeriodBoundariesMinMax, sdmxPeriodBoundaries } from '../../lib/settings';

export default compose(
  connect(createStructuredSelector({ period: getPeriod }), { changePeriod }),
  withProps({
    //titleLabel: <FormattedMessage id="de.filter.period.title" />,
    titleLabel: '',
    //tagLabel: <FormattedMessage id="de.filter.period.tag" values={sdmxPeriodBoundariesMinMax} />,
    tagLabel: '',
    periodBoundaries: sdmxPeriodBoundaries,
    options: { ...sdmxPeriodBoundariesMinMax, labelStepSize: 10 },
  }),
  pure,
)(DEFilterPeriod);
