import React from 'react';
import PropTypes from 'prop-types';
import { DETableCell } from '@sis-cc/dotstatsuite-ui-components';
import glamorous from 'glamorous';

const StyledCell = glamorous.div(
  {
    width: '100%',
    height: '100%',
    padding: '4px 6px',
    fontSize: 16,
    textAlign: 'right',
  },
  ({ isYLast, isXLast, theme }) => ({
    color: theme.table.oFont,
    borderTop: `1px solid ${theme.table.border}`,
    borderLeft: `1px solid ${theme.table.border}`,
    borderRight: isYLast ? `1px solid ${theme.table.border}` : null,
    borderBottom: isXLast ? null : `1px solid ${theme.table.border}`,
    backgroundColor: theme.table.xBg,
  }),
);

const Cell = ({ renderObservation, variant }) => (
  <React.Fragment>
    <StyledCell isXLast isYLast>
      <DETableCell value="Value" variant={variant} />
    </StyledCell>
    <StyledCell isYLast>{renderObservation()}</StyledCell>
  </React.Fragment>
);

Cell.propTypes = {
  renderObservation: PropTypes.func,
  variant: PropTypes.string,
};

export default Cell;
