import * as R from 'ramda';

/*
Returns a copy of the dataset values only keeping the latest datapoint for any combination of dimensions
e.g:
AGO:CME_MR0T5:_T:1990
AGO:CME_MR0T5:_T:2000
AGO:CME_MR0T5:_T:1995
get merged into:
AGO:CME_MR0T5:_T:2000 and returned with a new key where the year dimension is replaced by a _ (AGO:CME_MR0T5:_T:_)
The year value becames an attribute and gets appended at the end of the attributes array.
*/

export const getLatestDatapoints = data => {
  const TIME_PERIOD_ID = 'TIME_PERIOD';
  let time_dim_keyPosition = -1;
  let time_dim_values = null;
  let time_key_priority = [];
  let new_keys = {};

  for (let i = 0; i < data.structure.dimensions.observation.length; i++) {
    if (data.structure.dimensions.observation[i].id == TIME_PERIOD_ID) {
      time_dim_keyPosition = i;
      time_dim_values = [...data.structure.dimensions.observation[i].values];
      break;
    }
  }

  for (let i = 0; i < time_dim_values.length; i++) {
    time_dim_values[i]['key_id'] = i;
  }
  //inverse sort
  time_dim_values.sort((a, b) => {
    if (a.start < b.start) return 1;
    if (a.start > b.start) return -1;
    return 0;
  });

  time_dim_values.forEach(v => {
    time_key_priority.push('' + v.key_id);
  });

  let recent_time_key = '';
  let time_score = -1;

  for (const [k, v] of Object.entries(data.dataSets[0].observations)) {
    recent_time_key = k.split(':');
    time_score = time_key_priority.indexOf(recent_time_key[time_dim_keyPosition]);
    recent_time_key[time_dim_keyPosition] = '_';
    recent_time_key = recent_time_key.join(':');
    if (new_keys[recent_time_key] && new_keys[recent_time_key] > time_score) {
      new_keys[recent_time_key] = time_score;
    } else {
      new_keys[recent_time_key] = time_score;
    }
  }

  let ret = {};
  let getkey = '';
  let obs_val_attribs = null;
  for (const [k, v] of Object.entries(new_keys)) {
    getkey = k.replace('_', time_key_priority[v]);
    obs_val_attribs = data.dataSets[0].observations[getkey];
    ret[k] = [...obs_val_attribs];
    //adds the time label
    ret[k].push(
      data.structure.dimensions.observation[time_dim_keyPosition].values[time_key_priority[v]].id,
    );
  }

  return ret;
};
