import React from 'react';
import PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';
import { VXLoader } from '@sis-cc/dotstatsuite-ui-components';
import { RulesDriver } from '@sis-cc/dotstatsuite-components';
import Tools from '../Tools';
import Viewer from '../Viewer';
import Footnotes from '../Footnotes';

const Main = ({
  data,
  enhancedData,
  isRtl,
  isNarrow,
  isFullscreen,
  isLoadingData,
  display,
  formaterIds,
  type,
  options,
  dataUrl,
}) => (
  <RulesDriver
    data={enhancedData}
    display={display}
    formaterIds={formaterIds}
    type={type}
    options={options}
    sdmxUrl={dataUrl}
    source={{ link: window.location.href }}
    render={({ chartData, chartOptions, properties }) => (
      <div>
        {isLoadingData && (
          <VXLoader
            isNarrow={isNarrow}
            isRtl={isRtl}
            loadingLabel={<FormattedMessage id="de.visualisation.data.loading" />}
            isLarge
          />
        )}
        {!isLoadingData && (
          <React.Fragment>
            <Viewer
              chartData={chartData}
              chartOptions={chartOptions}
              data={data}
              isNarrow={isNarrow}
              isRtl={isRtl}
            />
            <br />
            <Footnotes />
            <div style={{ textAlign: 'right', fontWeight: '300' }}>V1.15</div>
          </React.Fragment>
        )}
      </div>
    )}
  />
);

Main.propTypes = {
  data: PropTypes.object,
  enhancedData: PropTypes.object,
  display: PropTypes.string,
  formaterIds: PropTypes.object,
  type: PropTypes.string,
  options: PropTypes.object,
  dataUrl: PropTypes.string,
  isNarrow: PropTypes.bool,
  isRtl: PropTypes.bool,
  isLoadingData: PropTypes.bool,
  isFullscreen: PropTypes.bool,
};

export default Main;
