import React from 'react';
import PropTypes from 'prop-types';
import { VXFilterContainer } from '@sis-cc/dotstatsuite-ui-components';
import FilterPeriod from '../FilterPeriod';
import FilterSelection from '../FilterSelection';
import Filters from '../Filters';
import Toolbar from '../Toolbar';
import TableConfig from '../TableConfig';

const Tools = ({
  isNarrow,
  isRtl,
  isFullscreen,
  actionId,
  /*isChartConfig,*/
  isFilters,
}) => (
  <React.Fragment>
    <Toolbar isNarrow={isNarrow} isRtl={isRtl} isFull={isFullscreen} actionId={actionId} />
    {/*<VXFilterContainer isOpen={isChartConfig} noScroll noHeader>
      <ChartConfig properties={properties} />
    </VXFilterContainer>*/}
    {isNarrow && (
      <VXFilterContainer isOpen={isFilters} noScroll noHeader>
        <FilterSelection isNarrow={isNarrow} isRtl={isRtl} />
        <FilterPeriod isNarrow={isNarrow} isRtl={isRtl} />
        <Filters isNarrow={isNarrow} isRtl={isRtl} />
        <TableConfig isNarrow={isNarrow} isRtl={isRtl} />
      </VXFilterContainer>
    )}
  </React.Fragment>
);

Tools.propTypes = {
  isNarrow: PropTypes.bool,
  isRtl: PropTypes.bool,
  isFullscreen: PropTypes.bool,
  isChartConfig: PropTypes.bool,
  isFilters: PropTypes.bool,
  actionId: PropTypes.string,
};

export default Tools;
