import * as R from 'ramda';
import axios from 'axios';
import FileSaver from 'file-saver';
import { rules } from '@sis-cc/dotstatsuite-components';
import {
  structureParser,
  getConfig,
  getFilename,
  getFusionConfig,
  dataflowsParser,
  hierarchyParser,
  getDataflowsConfig,
  getHierarchyUrl,
} from '../lib/sdmx';
import { sdmxRange, getRoundData } from '../lib/settings';
import * as fm from './formatMapper';
import { generateExcel, generateExcelLong } from '../lib/excel';

const getLocale = R.pathOr('en', ['locale', 'id']);
//const keysLimit = 800;

const getStructure = ({ configArgs }) => {
  const { url, datasource } =
    R.prop('backendId', configArgs.dataflow) == 'FUSION'
      ? getFusionConfig({ configArgs })
      : getConfig({ type: 'structure' })({ configArgs });

  const qStringParams =
    R.prop('backendId', configArgs.dataflow) == 'FUSION'
      ? //{ references: 'all', format: 'sdmx-json', detail: 'referencepartial'}
        { references: 'all', format: 'sdmx-json', detail: 'full' }
      : // { format: 'sdmx-json', mode: 'exact', references: 'descendants' }
        { references: 'all' };

  const acceptParam =
    R.prop('backendId', configArgs.dataflow) == 'FUSION'
      ? {
          'Accept-Language': getLocale(configArgs),
        }
      : {
          Accept: 'application/vnd.sdmx.structure+json;version=1.0',
          'Accept-Language': getLocale(configArgs),
        };

  const addForcedDims = (configArgs, struct) => {
    if (!configArgs.dataflow.forcedDims) return;
    Object.keys(configArgs.dataflow.forcedDims).forEach(k => {
      struct.data.dataStructures[0].dataStructureComponents.dimensionList.dimensions.forEach(d => {
        if (d.id == k) {
          d.forced = configArgs.dataflow.forcedDims[k];
        }
      });
    });
  };

  const mapSDMX_structure = data => {
    addForcedDims(configArgs, data.data);
    return data.data;
  };
  const mapSDMX_structureFusion = data => {
    addForcedDims(configArgs, data.data);
    return fm.mapStructure(data.data, configArgs.locale.id);
  };

  const sdmxMapperStruct =
    R.prop('backendId', configArgs.dataflow) == 'FUSION'
      ? mapSDMX_structureFusion
      : mapSDMX_structure;

  return axios
    .get(url, {
      params: R.cond([
        [
          R.always(R.prop('supportsReferencePartial', datasource)),
          R.assoc('detail', 'referencepartial'),
        ],
        [R.T, R.identity],
      ])(qStringParams),
      headers: acceptParam,
    })
    .then(R.pipe(sdmxMapperStruct, structureParser));
};

const getData = ({ configArgs, requestArgs, dimensions }) => {
  const { url, datasource } = getConfig({ type: 'data' })({ configArgs });

  if (requestArgs.lastnobservations) {
    requestArgs.lastnobservations = 1;
  } else {
    delete requestArgs.lastnobservations;
  }

  const mapSDMX_data = data => {
    return data.data;
  };
  const mapSDMX_dataFusion = data => {
    return fm.mapData(data.data, configArgs.locale.id, getRoundData);
  };

  const sdmxMapperData =
    R.prop('backendId', configArgs.dataflow) == 'FUSION' ? mapSDMX_dataFusion : mapSDMX_data;

  return axios
    .get(url, {
      params: R.mergeRight({ dimensionAtObservation: 'AllDimensions' }, requestArgs),
      headers: R.cond([
        [
          R.always(R.prop('hasRangeHeader', datasource)),
          R.assoc('Range', `values=${R.head(sdmxRange)}-${R.last(sdmxRange)}`),
        ],
        [R.T, R.identity],
      ])({
        Accept: 'application/vnd.sdmx.data+json;version=1.0.0',
        'Accept-Language': getLocale(configArgs),
      }),
    })
    .then(R.pipe(sdmxMapperData, rules.v8Transformer));
};

const getDataFile = ({ configArgs, requestArgs }) => {
  const { url } = getConfig({ type: 'data' })({ configArgs });

  if (requestArgs.lastnobservations) {
    requestArgs.lastnobservations = 1;
  } else {
    delete requestArgs.lastnobservations;
  }

  let accept = 'application/vnd.sdmx.data+csv;file=true';
  /*
  Re enable to have the labels only csv
  if (configArgs.dataflow.backendId && configArgs.dataflow.backendId == 'FUSION') {
    requestArgs['format'] = 'csv';
    requestArgs['labels'] = 'name';
    accept = '';
  }
  */

  return axios
    .get(url, {
      params: requestArgs,
      headers: {
        Accept: accept,
        'Accept-Language': getLocale(configArgs),
      },
      responseType: 'blob',
    })
    .then(response => {
      const blob = new Blob([R.prop('data')(response)], {
        type: R.pathOr('text/csv', ['headers', 'content-type'])(response),
      });
      FileSaver.saveAs(blob, `${getFilename(configArgs)}.csv`);
    });
};

const getExcelDataFile = ({ configArgs, requestArgs, layout, timeDimensionInverted }) => {
  const { url, datasource } = getConfig({ type: 'data' })({ configArgs });

  if (requestArgs.lastnobservations) {
    requestArgs.lastnobservations = 1;
  } else {
    delete requestArgs.lastnobservations;
  }

  return (
    axios
      .get(url, {
        params: R.mergeRight({ dimensionAtObservation: 'AllDimensions' }, requestArgs),
        headers: R.cond([
          [
            R.always(R.prop('hasRangeHeader', datasource)),
            R.assoc('Range', `values=${R.head(sdmxRange)}-${R.last(sdmxRange)}`),
          ],
          [R.T, R.identity],
        ])({
          Accept: 'application/vnd.sdmx.data+json;version=1.0.0',
          'Accept-Language': getLocale(configArgs),
        }),
      })
      //.then(R.pipe(R.prop('data'), rules.v8Transformer));
      .then(response => {
        generateExcel(response.data, layout, timeDimensionInverted).then(response => {
          const blob = new Blob([response], { type: 'application/vnd.ms-excel' });
          //FileSaver.saveAs(blob, `data.xlsx`);
          var yStart = '';
          var yEnd = '';
          if (requestArgs.startPeriod) yStart = requestArgs.startPeriod;
          if (requestArgs.endPeriod) yEnd = requestArgs.endPeriod;
          FileSaver.saveAs(blob, `${configArgs.dataflow.dataflowId}_${yStart}-${yEnd}.xlsx`);
        });
      })
  );
};

const getExcelDataFileLong = ({ configArgs, requestArgs }) => {
  const { url } = getConfig({ type: 'data' })({ configArgs });

  if (requestArgs.lastnobservations) {
    requestArgs.lastnobservations = 1;
  } else {
    delete requestArgs.lastnobservations;
  }

  let accept = 'application/vnd.sdmx.data+csv;file=true';

  if (configArgs.dataflow.backendId && configArgs.dataflow.backendId == 'FUSION') {
    requestArgs['format'] = 'csv';
    requestArgs['labels'] = 'name';
    accept = '';
  }

  return axios
    .get(url, {
      params: requestArgs,
      headers: {
        Accept: accept,
        'Accept-Language': getLocale(configArgs),
      },
      responseType: 'utf8',
    })
    .then(response => {
      generateExcelLong(response.data).then(response => {
        const blob = new Blob([response], { type: 'application/vnd.ms-excel' });
        //FileSaver.saveAs(blob, `data.xlsx`);
        var yStart = '';
        var yEnd = '';
        if (requestArgs.startPeriod) yStart = requestArgs.startPeriod;
        if (requestArgs.endPeriod) yEnd = requestArgs.endPeriod;
        FileSaver.saveAs(blob, `${configArgs.dataflow.dataflowId}_${yStart}-${yEnd}.xlsx`);
      });
    });
};

//Is any mapping needed for Fusion?
//The getDataflows method has been implemented working with fusion map dotStat id needed
const sdmxMapperDataflows = dataflows => {
  //No mapping needed for Fusion, just return
  return dataflows;
};

//Is any mapping needed for Fusion?
//The getHierarchy method has been implemented working with fusion map dotStat if needed
const sdmxMapperHierarchy = hierarchy => {
  //No mapping needed for Fusion, just return
  return hierarchy.data;
};

const getDataflows = ({ configArgs }) => {
  const { url, datasource } =
    R.prop('backendId', configArgs.dataflow) == 'FUSION'
      ? getDataflowsConfig({ configArgs })
      : getConfig({ type: 'dataflows' })({ configArgs });

  return axios
    .get(url, {
      params: {
        format: 'sdmx-json',
        detail: 'full',
        references: 'children',
        includeMetadata: true,
        /*includeMetrics: false,*/
        /*includeAllAnnotations: false,*/
      },
      headers: { Accept: 'application/json', 'Accept-Language': getLocale(configArgs) },
    })
    .then(R.pipe(sdmxMapperDataflows, dataflowsParser));
};

const getHierarchy = ({ configArgs }) => {
  const url = getHierarchyUrl({ configArgs });
  return axios
    .get(url, {
      params: {
        format: 'sdmx-json',
        detail: 'full',
        references: 'children',
      },
      headers: { Accept: 'application/json', 'Accept-Language': getLocale(configArgs) },
    })
    .then(R.pipe(sdmxMapperHierarchy, hierarchyParser));
};

const getMap = ({ url }) => {
  return axios
    .get(url, {
      params: {},
      headers: {},
    })
    .then(data => {
      return data.data;
    });
};

const methods = {
  getStructure,
  getData,
  getDataFile,
  getDataflows,
  getHierarchy,
  getExcelDataFile,
  getExcelDataFileLong,
  getMap,
};

const error = method => () => {
  throw new Error(`Unkown method: ${method}`);
};
const main = ({ method, ...rest }) => (methods[method] || error(method))(rest);
R.compose(R.forEach(([name, fn]) => (main[name] = fn)), R.toPairs)(methods);

export default main;
