import * as R from 'ramda';
import qs from 'qs';
import { getDatasource } from '../settings';
import { rules } from '@sis-cc/dotstatsuite-components';
export { default as structureParser } from './structure';
export { default as dataflowsParser } from './dataflows';
export { default as hierarchyParser } from './hierarchy';

export const getDataflowsConfig = ({ configArgs }) => {
  const { datasourceId } = R.prop('dataflow', configArgs);
  const datasource = getDatasource(datasourceId);

  return {
    url: `${R.prop('url', datasource)}/dataflow/all/all/latest/`,
    datasource,
  };
};

export const getHierarchyUrl = ({ configArgs }) => {
  const { datasourceId } = R.prop('dataflow', configArgs);
  const datasource = getDatasource(datasourceId);

  let ag = configArgs.config.hierarchy.agencyId;
  let id = configArgs.config.hierarchy.id;

  return `${R.prop('url', datasource)}/hierarchicalcodelist/${ag}/${id}/latest/`;
};

export const getFusionConfig = ({ configArgs }) => {
  const { datasourceId, dataflowId, agencyId, version } = R.prop('dataflow', configArgs);

  const datasource = getDatasource(datasourceId);
  if (R.isNil(datasource)) throw new Error(`Datasource undefined: datasourceId=${datasourceId}`);

  //const identifiers = R.join(',', [agencyId, dataflowId, R.defaultTo('latest', version)]);
  const identifiers = R.join('/', [agencyId, dataflowId, R.defaultTo('latest', version)]);
  return {
    url: `${R.prop('url', datasource)}/dataflow/${identifiers}`,
    datasource,
  };

  /*return {
    url: `${R.prop('url', datasource)}/dataflow/${identifiers}`,
    datasource,
  };*/
};

export const getConfig = ({ type }) => ({ configArgs }) => {
  if (R.not(R.includes(type, ['structure', 'data']))) throw new Error(`Invalid type: type=${type}`);

  const { datasourceId, dataflowId, agencyId, version } = R.prop('dataflow', configArgs);

  const datasource = getDatasource(datasourceId);
  if (R.isNil(datasource)) throw new Error(`Datasource undefined: datasourceId=${datasourceId}`);

  const isStructure = R.equals(type, 'structure');

  const keyword = isStructure ? 'dataflow' : 'data';

  const identifiers = R.join(isStructure ? '/' : ',', [
    agencyId,
    dataflowId,
    R.defaultTo('latest', version),
  ]);

  const dataquery = isStructure ? '' : `/${R.defaultTo('all', R.prop('dataquery', configArgs))}`;

  return {
    url: `${R.prop('url', datasource)}/${keyword}/${identifiers}${dataquery}`,
    datasource,
  };
};

export const getDataUrl = ({ configArgs, requestArgs }) => {
  const period = R.prop('period', requestArgs);
  const url = R.prop('url', getConfig({ type: 'data' })({ configArgs }));
  const params = qs.stringify(
    { startPeriod: R.head(period), endPeriod: R.last(period) },
    { addQueryPrefix: true },
  );

  return `${url}${params}`;
};

export const getFilename = R.pipe(
  R.converge(R.append, [
    R.pipe(R.prop('dataquery'), R.ifElse(R.isNil, R.always('all'), R.identity)),
    R.pipe(
      R.prop('dataflow'),
      R.pick(['datasourceId', 'dataflowId', 'agencyId', 'version']),
      R.values,
    ),
  ]),
  R.join('_'),
);
//The updateDataquery toggles filters, this function adds multiple codes to the dimension's filter
export const forceMultipleDataqueryUpdate = dimensions => (filterId, values) => dataquery => {
  if (R.isNil(filterId)) return R.reduce(R.concat('.'), '', dimensions);

  const filterIndex = R.findIndex(R.propEq('id', filterId), dimensions);

  const safeDataquery = R.isNil(dataquery) ? R.reduce(R.concat('.'), '', dimensions) : dataquery;

  // undefined valueId <=> remove filter from selection
  if (R.isNil(values)) {
    return R.pipe(R.split('.'), R.update(filterIndex, ''), R.join('.'))(safeDataquery);
  }

  const dotSplitted = safeDataquery.split('.');
  let newDimensionQuery = [];
  if (dotSplitted[filterIndex] !== '') {
    newDimensionQuery = dotSplitted[filterIndex].split('+');
  }
  values.forEach(v => {
    if (!newDimensionQuery.includes(v)) newDimensionQuery.push(v);
  });

  dotSplitted[filterIndex] = newDimensionQuery.join('+');
  return dotSplitted.join('.');
};

export const updateDataquery = dimensions => (filterId, valueId) => dataquery => {
  if (R.isNil(filterId)) return R.reduce(R.concat('.'), '', dimensions);

  const filterIndex = R.findIndex(R.propEq('id', filterId), dimensions);

  if (dimensions[filterIndex].forced != null) {
    valueId = dimensions[filterIndex].forced;
  }

  const safeDataquery = R.isNil(dataquery) ? R.reduce(R.concat('.'), '', dimensions) : dataquery;

  // undefined valueId <=> remove filter from selection
  if (R.isNil(valueId)) {
    return R.pipe(R.split('.'), R.update(filterIndex, ''), R.join('.'))(safeDataquery);
  }

  return R.pipe(
    R.split('.'),
    R.over(
      R.lensIndex(filterIndex),
      R.pipe(
        R.ifElse(R.isEmpty, R.always([]), R.split('+')),
        R.ifElse(R.includes(valueId), R.without([valueId]), R.append(valueId)),
        R.join('+'),
      ),
    ),
    R.join('.'),
  )(safeDataquery);
};

const greaterThanOne = R.pipe(R.length, R.flip(R.gt)(1));
const isMany = d =>
  R.or(rules.isTimeDimension(R.prop('id')(d)), greaterThanOne(R.prop('values')(d)));
export const getDimensionsIds = R.pipe(R.filter(isMany), R.map(R.prop('id')));
export const getUniq = (array1, array2) => R.pipe(R.concat(array2), R.uniq)(array1);
