//The lang is requested to the SDMX registry in the GET request.
//The sdmx registry returns a node with the requesnted lang (defaults to en if missing).
//Just get the value of the first returned node
const getLocalisedName = node => {
  if (!node || node.length == 0) return '';
  return node[0].value;
};

const getAnyNode = (node, alt) => {
  if (!node || !alt) return null;
  for (let i = 0; i < alt.length; i++) {
    if (node.hasOwnProperty(alt[i])) return node[alt[i]];
  }
  return null;
};

const getDimensions = (ref, dsds) => {
  //"dataStructureRef": "urn:sdmx:org.sdmx.infomodel.datastructure.DataStructure=UNESCO:EDU_NON_FINANCE(3.0)"

  //Could use a regex but it seems that Firefox doesn't support lookbehind
  const dsdId = ref.split('=')[1];
  const agency = dsdId.split(':')[0]; //Split and take agency
  const id = dsdId.split(':')[1].split('(')[0]; //Split and take Dataflow
  const v = dsdId
    .split(':')[1]
    .split('(')[1]
    .slice(0, -1); //split and take version

  //Get the DSD matching the agency, dsd and version
  const dsd = dsds.filter(el => {
    return el.agencyID == agency && el.id == id && el.version == v;
  })[0];

  if (!dsd) return null;
  const ret = [];
  dsd.dataStructureComponents.dimensionList.dimensions.forEach(el => {
    //the urn is urn:sdmx:org.sdmx.infomodel.datastructure.TimeDimension=UNICEF_TEST:DSD_DHIS_SAMPLE_2(1.0).TIME_PERIOD
    //use the id in this case
    ret.push(el.id);
  });

  return ret;
};

const getDataflow = (df, dsds) => {
  return {
    id: df.id,
    name: df.name,
    agencyID: df.agencyID,
    version: df.version,
    isFinal: df.isFinal,
    dimensions: getDimensions(df.structure, dsds),
  };
};

export default dataflowsParser => {
  const ret = {};
  if (!dataflowsParser.data.data.dataflows) return ret;
  dataflowsParser.data.data.dataflows.forEach(element => {
    ret[element.id] = getDataflow(element, dataflowsParser.data.data.dataStructures);
  });
  return ret;
};
