import React from 'react';
import { FormattedMessage } from 'react-intl';

export default ({
  requestDataFile,
  requestExcelDataFile,
  requestExcelDataFileLong,
  dataflow,
  changeViewer,
  viewerId,
  map,
  mapRefAreaDimId,
  mapAreasToPreselect,
  helpUrl,
  changeDataqueryMultipleUpdate,
}) => {
  const actionsTable = [
    {
      icon: 'import',
      id: 'download',
      label: <FormattedMessage id="de.visualisation.toolbar.action.download" />,
      children: [
        {
          id: 'excel-long',
          label: (
            <FormattedMessage id="de.visualisation.toolbar.action.download.excel.selection.long" />
          ),
          action: () => requestExcelDataFileLong({ isDownloadAllData: false, dataflow }),
        },
        {
          id: 'excel-selection',
          label: <FormattedMessage id="de.visualisation.toolbar.action.download.excel.selection" />,
          action: () => requestExcelDataFile({ isDownloadAllData: false, dataflow }),
        },
        {
          id: 'csv-selection',
          label: <FormattedMessage id="de.visualisation.toolbar.action.download.csv.selection" />,
          action: () => requestDataFile({ dataflow }),
        },
        {
          id: 'csv-full',
          label: <FormattedMessage id="de.visualisation.toolbar.action.download.csv.all" />,
          action: () => requestDataFile({ isDownloadAllData: true, dataflow }),
        },
      ],
    },
  ];

  const actionMap = {
    icon: 'map',
    id: 'map',
    label: 'Map',
    action: () => {
      changeDataqueryMultipleUpdate(mapRefAreaDimId, mapAreasToPreselect), changeViewer('map');
    },
  };

  if (typeof map != 'undefined' && map) {
    actionsTable.push(actionMap);
  }

  const actionHelp = {
    icon: 'help',
    id: 'help',
    label: 'Help',
    action: () => {
      window.open(helpUrl, '_self');
    },
  };

  const actionsMap = [
    {
      icon: 'th',
      id: 'table',
      label: 'Table',
      action: () => changeViewer('table'),
    },
  ];

  if (helpUrl) {
    actionsTable.push(actionHelp);
    actionsMap.push(actionHelp);
  }

  if (viewerId == 'table') return actionsTable;
  if (viewerId == 'map') return actionsMap;
};
