import React from 'react';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { compose, withProps, pure } from 'recompose';
import { FormattedMessage } from 'react-intl';
import { VXFiltersCurrent } from '@sis-cc/dotstatsuite-ui-components';
import { changeDataquery } from '../../ducks/vis';
import { getSelection } from '../../selectors/sdmx';

export default compose(
  connect(createStructuredSelector({ items: getSelection }), { changeFilters: changeDataquery }),
  withProps({
    titleLabel: <FormattedMessage id="vx.filters.current.title" />,
    clearAllLabel: <FormattedMessage id="vx.filters.current.clear" />,
    blankLabel: <FormattedMessage id="vx.filters.current.blank" />,
    noScroll: true,
  }),
  pure,
)(VXFiltersCurrent);
