import React from 'react';
import { render } from 'react-dom';
import { Provider } from 'react-redux';
import '@blueprintjs/core/dist/blueprint.css';
import { I18nProvider, initialize as initializeI18n } from './i18n';
import { createStore, reducer } from './core';
import ThemeProvider from './theme';
import ErrorBoundary from './components/Error';
import App from './components/App';
import { changeDataflow, loadDataflows, loadHierarchy, loadMap } from './ducks/vis';
import './index.css';
import meta from '../../package.json';

var ES6_Error = false;
if (!(typeof window.ES6_Error === 'undefined')) {
  ES6_Error = window.ES6_Error;
}
if (ES6_Error) {
  throw new Error('Browser not Supported');
}

console.info(`${meta.name}@${meta.version}`); // eslint-disable-line no-console

initializeI18n(window.SETTINGS.i18n);
const locale = window.SETTINGS.i18n.locales[window.SETTINGS.i18n.localeId];

//Is the lastnobs param active?
let lnobs = false;
if (window.DATAFLOW.lastnobservations && window.DATAFLOW.lastnobservations > 0) {
  lnobs = true;
}

const initialState = {
  app: {
    logs: [],
    pending: {},
    locale,
  },
  vis: {
    filter: undefined, // id of the expanded filter: ''
    viewer: 'table', // id of the viewer element to display
    action: undefined, // id of the current tool action: ''
    period: undefined, // period: [start, end]
    dataflow: undefined, // dataflow identifiers: {datasourceId, dataflowId, agencyId, version}
    isFullscreen: false, // fullscreen mode
    dataquery: undefined, // dataquery: ''
    display: 'label', // sdmx display mode: '' in label, code, both
    timeDimensionInverted: { TIME_PERIOD: true }, // sdmx time direction: obj of bools, true asc - false desc
    layout: undefined, // layout of the table
    dimensionGetter: 'label', // accessor of the dimension for labelling,
    lastnobservations: lnobs, //is the lastnObservation option checked?,
    map: undefined, //the map
    helpUrl: null,
  },
  sdmx: {
    dimensions: [],
    data: undefined,
    layout: {},
    dataflows: {},
    map_selection: {}, //the map selection
  },
};
if (window.SETTINGS.helpUrl) {
  initialState.vis.helpUrl = window.SETTINGS.helpUrl;
}
if (window.SETTINGS.timeDimensionOrder) {
  if (window.SETTINGS.timeDimensionOrder == 'asc') {
    initialState.vis.timeDimensionInverted.TIME_PERIOD = false;
  } else if (window.SETTINGS.timeDimensionOrder == 'desc') {
    initialState.vis.timeDimensionInverted.TIME_PERIOD = true;
  }
}

const store = createStore(initialState, reducer);

console.log('initialized with DATAFLOW:', window.DATAFLOW); // eslint-disable-line no-console
store.dispatch(changeDataflow(window.DATAFLOW));
store.dispatch(loadDataflows());

let map_dataflow_id = window.DATAFLOW.agencyId + ':' + window.DATAFLOW.dataflowId;
if (
  window.SETTINGS.map &&
  window.SETTINGS.map[map_dataflow_id] &&
  window.SETTINGS.map[map_dataflow_id].geojson_url
) {
  store.dispatch(loadMap(window.SETTINGS.map[map_dataflow_id].geojson_url));
}
if (window.SETTINGS.hierarchy && window.SETTINGS.hierarchy.agencyId) {
  store.dispatch(loadHierarchy(window.SETTINGS, window.DATAFLOW));
}

const ROOT = document.getElementById('root');
ROOT.addEventListener('unicef_changeDataflow', event =>
  store.dispatch(changeDataflow(event.detail)),
);
console.log(`listen unicef_changeDataflow event`); // eslint-disable-line no-console

render(
  <ErrorBoundary isFinal>
    <Provider store={store}>
      <ThemeProvider theme={{}} isRtl={locale.isRtl}>
        <I18nProvider localeId={locale.id} messages={window.I18N}>
          <ErrorBoundary>
            <App />
          </ErrorBoundary>
        </I18nProvider>
      </ThemeProvider>
    </Provider>
  </ErrorBoundary>,
  ROOT,
);
