import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { getSelection, getDataflows } from '../../selectors/sdmx';
import { getDataflow, getPeriod } from '../../selectors/vis';
import { FormattedMessage } from 'react-intl';

//build the selection for each dimension e.g. REF_AREA = AFG+ITA
const dim_selection = (df_id, dim, selection) => {
  if (!selection) return '';

  //is it there a selection for the dimension?
  let sel = selection.filter(s => s.id == dim);
  if (sel.length == 0) return ''; //no-> return

  return sel[0].values.map(elem => elem.id).join('+');
};

//creates the link to the specific sector's dataflow
const createLink = (df_id, dataflow, selection, period, stickyDims) => {
  //build the dataquery, for each dimension compose the list of selected codes (AFG+ITA.U5MR._T)
  let dq = [];

  dataflow.dimensions.forEach(dim => {
    //The sticky behaviour is applied only to a limited set of dims
    if (stickyDims.indexOf(dim) >= 0) {
      //build the selection
      dq.push(dim_selection(df_id, dim, selection));
    } else {
      dq.push('');
    }
  });

  //get the first part of the url
  let oldUrl = window.location.href.split('?')[0];
  let ret = oldUrl + `?ag=${dataflow.agencyID}&df=${dataflow.id}&ver=${dataflow.version}`;

  ret = ret + `&dq=${dq.join('.')}`;
  ret = ret + `&startPeriod=${period[0]}&endPeriod=${period[1]}`;

  return ret;
};

//The user selected an indicator, is it also contained in another Dataflow? (Selected node's parent == DATAFLOW_ID?)
const containsIndicators = (df_id, selItems) => {
  if (!selItems) return false;
  //If the selection does not contain an indicator return
  const indDim = selItems.filter(ind => ind.id == 'INDICATOR');
  if (indDim.length == 0) return false;

  //if the parent id == the dataflow we're creating the link for then return true
  for (let i = 0; i < indDim[0].values.length; i++) {
    if (indDim[0].values[i].parentId == df_id) {
      return true;
    }
    //Parent!=DataflowID exception handling
    if (indDim[0].values[i].parentId == 'WS') {
      if (indDim[0].values[i].id.startsWith('WS_HCF') && df_id == 'WASH_HEALTHCARE_FACILITY')
        return true;
      if (indDim[0].values[i].id.startsWith('WS_PPL') && df_id == 'WASH_HOUSEHOLDS') return true;
      if (indDim[0].values[i].id.startsWith('WS_SCH') && df_id == 'WASH_SCHOOLS') return true;
    }
  }
  return false;
};

const isOnEvidence = (df_id, on_evidence) => {
  if (!on_evidence) return null;
  for (let i = 0; i < on_evidence.length; i++)
    if (df_id == on_evidence[i].id) {
      return on_evidence[i];
    }
  return null;
};

//A custom sort function: sorts the dataflows array keeping the ones onEvidence on top.
const dataflows_sort = (toSort, onEvidence) => {
  toSort.sort(function(a, b) {
    if (onEvidence.includes(a.id)) {
      return -1;
    }
    if (onEvidence.includes(b.id)) {
      return 1;
    } else {
      return a.name > b.name ? 1 : b.name > a.name ? -1 : 0;
    }
  });
};

const DataflowNavigator = props => {
  const dataflows = props.dataflows;

  if (!dataflows) return <div />;

  //filter/sort dataflows
  const df_keys = [];
  Object.keys(dataflows).forEach(df_key => {
    //add only if the dataflow has been marked as Final
    if (dataflows[df_key].isFinal) {
      df_keys.push({ id: df_key, name: dataflows[df_key].name });
    }
  });

  dataflows_sort(df_keys, props.dataflowsOnEvidence);

  //For each dataflow loop the keys
  const items = [];

  df_keys.forEach(k => {
    const df_key = k['id'];

    let actionText = <FormattedMessage id="de.dataflowNavigator.findMoreData" />;
    const onEvidence = isOnEvidence(df_key, props.dataflowsOnEvidence);
    if (onEvidence != null && onEvidence.actionText) {
      actionText = onEvidence.actionText;
    }

    if (
      df_key != props.dataflow.dataflowId &&
      (containsIndicators(df_key, props.selItems) ||
        isOnEvidence(df_key, props.dataflowsOnEvidence) != null)
    ) {
      items.push(
        <div key={df_key} className="dfNav_item">
          <div className="dfNav_dfnametop">{actionText}</div>
          <div className="df_nav_dfname">{dataflows[df_key].name}</div>
          <div className="dfNav_link">
            <a
              className="btn button"
              href={createLink(
                df_key,
                props.dataflows[df_key],
                props.selItems,
                props.period,
                props.stickyDims,
              )}
            >
              Go to dataset
            </a>
          </div>
        </div>,
      );
    }
  });

  if (items.length == 0) {
    return <div />;
  }
  return <div className="TableConfig_wrapper">{items}</div>;
};

DataflowNavigator.propTypes = {
  selItems: PropTypes.array,
  dataflow: PropTypes.object,
  dataflows: PropTypes.object,
  period: PropTypes.array,
  dataflowsOnEvidence: PropTypes.array,
  stickyDims: PropTypes.array,
};

const withProps = state => {
  return {
    selItems: getSelection(state),
    dataflow: getDataflow(state),
    dataflows: getDataflows(state),
    period: getPeriod(state),
  };
};

export default connect(withProps)(DataflowNavigator);
