import React from 'react';
import PropTypes from 'prop-types';

const FooterUnicef = ({ label, showCopyrightSymbol }) => {
  let copy = '\u00a9';
  return (
    <div className="div_footer">
      {showCopyrightSymbol ? copy : null}
      {label}
    </div>
  );
};

FooterUnicef.propTypes = {
  label: PropTypes.string,
  showCopyrightSymbol: PropTypes.bool,
};

export default FooterUnicef;
