import * as R from 'ramda';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { compose, branch, renderNothing } from 'recompose';
import { getIsPending, getIsRtl } from '../../selectors/app';
import { getIsFullscreen, getDataflow, getLastnobservations } from '../../selectors/vis';
import { getViewer } from '../../selectors/vis';

import withResponsiveness from '../../hocs/withResponsiveness';
import App from './App';

export default compose(
  withResponsiveness,
  connect(
    createStructuredSelector({
      isLoadingStructure: getIsPending('getStructure'),
      isLoadingData: getIsPending('getData'),
      isRtl: getIsRtl,
      isFullscreen: getIsFullscreen,
      viewerId: getViewer,

      dataflow: getDataflow,
      lastnobs_active: getLastnobservations,
      selItems: getSelection,
    }),
  ),
  branch(R.pipe(R.prop('dataflow'), R.isNil), renderNothing),
)(App);
