import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';
import { CHANGE_LASTNOBS } from '../../ducks/vis';

class Lastnobs extends React.Component {
  render() {
    return (
      <div className="div_lastnobs">
        <label>
          <input
            name="lastnobs"
            type="checkbox"
            checked={this.props.lastnobs}
            onChange={() => this.props.changeLastnobs(!this.props.lastnobs)}
          />
          <FormattedMessage id="de.filter.period.lastnobs" />
        </label>
      </div>
    );
  }
}

Lastnobs.propTypes = {
  lastnobs: PropTypes.bool,
  changeLastnobs: PropTypes.func,
};

const withProps = state => {
  let lastn = false;
  if (state.vis && state.vis.lastnobservations) {
    lastn = true;
  }
  return {
    lastnobs: lastn,
  };
};

function mapDispatchToProps(dispatch, props) {
  return {
    changeLastnobs: lastnobs =>
      dispatch({
        type: CHANGE_LASTNOBS,
        payload: { lastnobservations: lastnobs },
      }),
  };
}

export default connect(withProps, mapDispatchToProps)(Lastnobs);
