import React from 'react';
import PropTypes from 'prop-types';
import { DETableConfig, VXFilterContainer } from '@sis-cc/dotstatsuite-ui-components';

const TableConfig = ({ title, ...tableProps }) => (
  <VXFilterContainer titleLabel={title} noCollapse noScroll>
    <DETableConfig {...tableProps} />
  </VXFilterContainer>
);

TableConfig.propTypes = {
  //title: PropTypes.object,
  title: PropTypes.string,
};

export default TableConfig;
