import React from 'react';
import { FormattedMessage } from 'react-intl';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { compose, branch, renderComponent, withProps } from 'recompose';
import * as R from 'ramda';
import { VXNoData } from '@sis-cc/dotstatsuite-ui-components';
import { getViewer } from '../../selectors/vis';
import Table from '../Table';
import Chart from '../Chart';
import MapWrapper from '../Map';
import viewerConfig from './config';

export default compose(
  connect(createStructuredSelector({ type: getViewer })),
  withProps({ viewerConfig }),
  branch(
    R.pipe(R.prop('data'), R.isNil),
    renderComponent(() => <VXNoData message={<FormattedMessage id="vx.no.data" />} />),
  ),
  branch(
    ({ type }) => R.equals(type, 'table'),
    renderComponent(Table),
    renderComponent(MapWrapper),
  ),
)(Chart);
