import React from 'react';
import PropTypes from 'prop-types';
import * as R from 'ramda';
import { VXFilterContainerCollapsible } from '@sis-cc/dotstatsuite-ui-components';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { FormattedMessage } from 'react-intl';

const Footnotes = ({ footnotes }) => (
  <VXFilterContainerCollapsible
    titleLabel={<FormattedMessage id="footnotes" />}
    isOpen={true}
    noScroll
  >
    <List dense>
      {R.map(({ id, label, values }) => (
        <ListItem key={id}>
          <ListItemText
            primary={R.defaultTo(id, label)}
            secondary={R.pipe(R.pluck('name'), R.join(' | '))(values)}
          />
        </ListItem>
      ))(footnotes)}
    </List>
  </VXFilterContainerCollapsible>
);

Footnotes.propTypes = {
  footnotes: PropTypes.array,
};

export default Footnotes;
