import { createStore, applyMiddleware, compose } from 'redux';
import thunk from 'redux-thunk';
import { createLogger } from 'redux-logger';
import { requestDataMiddleware } from './middlewares';

export const withLogger = middlewares => {
  if (process.env.NODE_ENV === 'production') return middlewares;

  const logger = createLogger({
    duration: true,
    timestamp: false,
    collapsed: true,
    diff: true,
  });

  return [...middlewares, logger];
};

export default (initialState = {}, rootReducer) => {
  const middlewares = [thunk, requestDataMiddleware];

  const enhancers = [applyMiddleware(...withLogger(middlewares))];

  const store = createStore(rootReducer, initialState, compose(...enhancers));

  return store;
};
