import { getCuratedData } from '@sis-cc/dotstatsuite-ui-components/build/components/de-table/lib/curator';
import { getSortedLayout } from '@sis-cc/dotstatsuite-ui-components/build/components/de-table/lib/tanker';
import ExcelJS from 'exceljs/dist/exceljs';

const TIME_PERIOD_ID = 'TIME_PERIOD';

const getDimensionById = (dims, id) => {
  for (var i = 0; i < dims.length; i++) {
    if (dims[i].id == id) {
      return dims[i];
    }
  }
  return null;
};

const updateIndex = (index, dimval) => {
  index[dimval.dimension.__index] = dimval.value.__index;
};

const idxToString = idx => {
  return idx
    .filter(e => {
      return e != null;
    })
    .join(':');
};

const createDataRow = (header, section, datarow, curated, obs, attribs) => {
  const hidx = [];
  const secidx = [];
  const valIdx = [];
  var rowDimVals = [];
  var row = [];
  var dataStartIdx = 0;
  var rowAttribs = [];

  //Adds the dimension values at the beginning of the data row (when something has benn put on the rows)
  //It is the first level in the "curated" structure
  datarow.forEach(dr => {
    rowDimVals.push(dr.value.name);
  });

  //For each header start to build the observation key:[headerKey1:headerKey2...] => [0:2]
  header.forEach(h => {
    if (h.length > 0) {
      h.forEach(h2 => {
        updateIndex(hidx, h2);
      });
    }
    var headerIndex = idxToString(hidx);

    //To the same for the section: [sectionKey1:sectionKey2....]
    //It is the second level in the "curated" structure
    section.forEach(sec => {
      updateIndex(secidx, sec);
    });
    const sectionIndex = idxToString(secidx);

    //Same for the row
    //It is the third level in the "curated" structure
    datarow.forEach(dr => {
      updateIndex(valIdx, dr);
    });
    const valIndex = idxToString(valIdx);

    //If header has no index then the key is ""
    var lev1 = null;
    if (!headerIndex) {
      lev1 = curated[''];
    } else {
      lev1 = curated[headerIndex];
    }

    dataStartIdx = row.length;

    const lev2 = lev1[sectionIndex];
    //Lev 2 is missing? Add a -
    if (!lev2) {
      row.push('-');
      rowAttribs.push('');
      //There is a lev 2 go to lev 3 in the structure
    } else {
      const lev3 = lev2[valIndex];
      //No lev 3 add -
      if (!lev3) {
        row.push('-');
        rowAttribs.push('');
      } else {
        //obs_val
        row.push(obs[lev3][0]);
        //attribs
        var att = [];
        for (var a = 1; a < obs[lev3].length; a++) {
          if (obs[lev3][a] != null) {
            //If there is more than 1 attribute value then add it to the notes, then it will be added to the footnotes.
            if (!attribs[a - 1].isFootnote) {
              att.push(attribs[a - 1].name + ': ' + attribs[a - 1].values[obs[lev3][a]].name);
            }
          }
        }

        rowAttribs.push(att.join('\r\n'));
      }
    }
  });

  return { rowDimVals: rowDimVals, row: row, rowAttribs: rowAttribs, dataStartIdx: dataStartIdx };
};

const createFootnotes = attribs => {
  const ret = [];

  attribs.forEach(a => {
    if (a.values && a.values.length != 0 && a.isFootnote) {
      ret.push({ name: a.name, value: a.values[0].name });
    }
  });

  return ret;
};

const changeCellStyle = (cell, style) => {
  cell.fill = {
    type: 'pattern',
    pattern: 'solid',
    fgColor: { argb: style.fill },
  };
  var isBold = false;
  if (style.bold) {
    isBold = true;
  }
  cell.font = { color: { argb: style.color }, bold: isBold };
};

export const generateExcel = async (data, layout, timeDimensionInverted) => {
  const excel_styles = {
    header: {
      fill: 'FF47494e',
      color: 'FFFFFFFF',
    },
    section: {
      fill: 'FF47494e',
      color: 'FFFFFFFF',
    },
    rowDim: {
      fill: 'FFd9d9d9',
      color: 'FF47494e',
      bold: true,
    },
    rowDimValue: {
      fill: 'FFd9d9d9',
      color: 'FF47494e',
    },
    footnotesSection: {
      bold: true,
    },
  };

  var sections = [];
  var header = [];
  var rows = [];

  const dims = data.data.structure.dimensions.observation;
  const attribs = data.data.structure.attributes.observation;
  const obs = data.data.dataSets[0].observations;

  attribs.forEach(a => {
    a.isFootnote = true;
  });

  var lastAttribKey = [];
  const obs_keys = Object.keys(obs);

  if (obs_keys.length > 1) {
    obs[obs_keys[0]].forEach(a => {
      lastAttribKey.push(a);
    });

    Object.keys(obs).forEach(o => {
      for (var oidx = 1; oidx < obs[o].length; oidx++) {
        if (lastAttribKey[oidx] != obs[o][oidx]) {
          attribs[oidx - 1].isFootnote = false;
        }
      }
    });
  }

  dims.forEach(d => {
    var vidx = 0;
    d['__index'] = d.keyPosition;
    d['label'] = d.name;
    d.values.forEach(v => {
      v['__index'] = vidx;
      v['label'] = v.name;
      vidx++;
    });
  });

  var timeDim = dims.find(o => {
    return o.id == TIME_PERIOD_ID;
  });
  const timeSort = function(a, b) {
    if (a.start < b.start) return -1;
    if (a.start > b.start) return 1;
    return 0;
  };
  const timeSortInv = function(a, b) {
    if (a.start > b.start) return -1;
    if (a.start < b.start) return 1;
    return 0;
  };
  if (timeDimensionInverted) {
    timeDim = timeDim.values.sort(timeSortInv);
  } else {
    timeDim = timeDim.values.sort(timeSort);
  }

  if (layout) {
    if (layout.sections) {
      layout.sections.forEach(element => {
        sections.push(getDimensionById(dims, element));
      });
    }
    if (layout.header) {
      layout.header.forEach(element => {
        header.push(getDimensionById(dims, element));
      });
    }
    if (layout.rows) {
      layout.rows.forEach(element => {
        rows.push(getDimensionById(dims, element));
      });
    }
  } else {
    rows = [dims[0]];
    sections = dims.slice(1, dims.length);
  }

  const layout2 = { header: header, rows: rows, sections: sections };
  const datakeys = [];
  for (let [key, value] of Object.entries(obs)) {
    datakeys.push(key);
  }

  const curated = getCuratedData({ observations: obs, layout: layout2 });
  const curLay = getSortedLayout(layout2, obs);

  //const ExcelJS = require('exceljs');
  var rowLen = 0;
  var rowDimsLen = 0;

  if (
    curLay.sectionsData[0] &&
    curLay.sectionsData[0][1] &&
    curLay.sectionsData[0][1][0] &&
    rowLen < curLay.sectionsData[0][1][0].length + 1
  ) {
    rowDimsLen = curLay.sectionsData[0][1][0].length;
  }

  if (curLay.headerData && curLay.headerData.length > 0) {
    //rowlen is header (e.g. #years) + 1 (dim title) + it gets longer id there are 2 or more rowDims: rowDimsLen
    //It should be #dimvalues on header + 1 (header title) + #rowDims -1(as one rowDim is under the header title)
    //= #dimValues + #rowDims +1 -1 = #dimValues + #rowDims
    rowLen = curLay.headerData.length + rowDimsLen;
  }

  const workbook = new ExcelJS.Workbook();
  const sheet = workbook.addWorksheet('Unicef data');

  var row = [];
  //Create the header (columns) rows
  if (curLay.headerData && curLay.headerData.length > 0) {
    for (var rnum = 0; rnum < curLay.headerData[0].length; rnum++) {
      row.push(curLay.headerData[0][rnum].dimension.name);

      for (var col_shift = 0; col_shift < rowDimsLen - 1; col_shift++) row.push('');

      curLay.headerData.forEach(h => {
        row.push(h[rnum].value.name);
      });
      var r = sheet.addRow(row);

      for (var i = 0; i < rowLen; i++) {
        changeCellStyle(r.getCell(i + 1), excel_styles.header);
      }

      row = [];
    }
  }

  //create the rows dimensions title row
  row = [];
  if (curLay.sectionsData && curLay.sectionsData.length > 0) {
    curLay.sectionsData[0][1][0].forEach(s => {
      row.push(s.dimension.name);
    });
    r = sheet.addRow(row);
    for (i = 0; i < rowLen; i++) {
      changeCellStyle(r.getCell(i + 1), excel_styles.rowDim);
    }
  }

  //create sections and data rows
  row = [];

  //console.log("data")
  //console.log(data)

  if (curLay.sectionsData) {
    curLay.sectionsData.forEach(sec => {
      var sectionText = '';
      sec[0].forEach(secHead => {
        sectionText = sectionText + secHead.dimension.name + ': ' + secHead.value.name + ' | ';
      });

      r = sheet.addRow([sectionText]);
      for (i = 0; i < rowLen; i++) {
        changeCellStyle(r.getCell(i + 1), excel_styles.section);
      }
      sec[1].forEach(datarow => {
        var rowData = createDataRow(curLay.headerData, sec[0], datarow, curated, obs, attribs);
        r = sheet.addRow();

        var cellCount = 1;
        var cell = null;
        for (i = 0; i < rowData.rowDimVals.length; i++) {
          cell = r.getCell(cellCount);
          cell.value = rowData.rowDimVals[i];
          changeCellStyle(cell, excel_styles.rowDimValue);
          cellCount++;
        }

        for (i = 0; i < rowData.row.length; i++) {
          cell = r.getCell(cellCount);
          cell.value = rowData.row[i];
          if (rowData.rowAttribs[i] != '') {
            cell.note = rowData.rowAttribs[i];
          }
          cellCount++;
        }

        row = [];
      });
    });
  }

  //The footnotes
  const footnotes = createFootnotes(attribs);
  if (footnotes.length > 0) {
    r = sheet.addRow();
    r = sheet.addRow(['Footnotes']);
    changeCellStyle(r.getCell(1), excel_styles.footnotesSection);
  }
  footnotes.forEach(f => {
    //r = sheet.addRow([f.name, f.value]);
    r = sheet.addRow([f.name + ': ' + f.value]);
  });

  const buf = await workbook.xlsx.writeBuffer();
  return buf;
};

const csvToArray = text => {
  let p = '',
    row = [''],
    ret = [row],
    i = 0,
    r = 0,
    s = !0,
    l;
  for (l of text) {
    if ('"' === l) {
      if (s && l === p) row[i] += l;
      s = !s;
    } else if (',' === l && s) l = row[++i] = '';
    else if ('\n' === l && s) {
      if ('\r' === p) row[i] = row[i].slice(0, -1);
      row = ret[++r] = [(l = '')];
      i = 0;
    } else row[i] += l;
    p = l;
  }
  return ret;
};

export const generateExcelLong = async data => {
  const excel_styles = {
    header: {
      fill: 'FF007cba',
      color: 'FFFFFFFF',
    },
  };

  const parsed = csvToArray(data);
  let r = null;
  let i = 0;
  const workbook = new ExcelJS.Workbook();
  const sheet = workbook.addWorksheet('Unicef data');
  if (parsed && parsed.length > 0) {
    r = sheet.addRow(parsed[0]);
    for (i = 0; i < parsed[0].length; i++) {
      changeCellStyle(r.getCell(i + 1), excel_styles.header);
    }
    for (i = 1; i < parsed.length; i++) {
      r = sheet.addRow(parsed[i]);
    }
  }

  const buf = await workbook.xlsx.writeBuffer();
  return buf;
};
