import React, { Children } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';
import FilterPeriod from '../FilterPeriod';
import FilterSelection from '../FilterSelection';
import Filters from '../Filters';
import Lastnobs from '../Lastnobs';
import DataflowNavigator from '../DataflowNavigator';

import { VXFilterContainerCollapsible } from '@sis-cc/dotstatsuite-ui-components';
import { showDataflowNavigator, getStickyDims, getDataflowsOnEvidence } from '../../lib/settings';

//settings

class NarrowFilters extends React.Component {
  constructor() {
    super();
    this.state = {
      isOpen: false,
      dateRangeIsOpen: false,
    };
  }

  render() {
    var toRender = null;
    if (!this.state.isOpen) {
      toRender = (
        <button
          className="narrow_btnOpenFilters"
          onClick={() => {
            this.setState({ isOpen: true });
          }}
        >
          <span className="pt-icon-standard pt-icon-settings" />
          <FormattedMessage id="vx.filters.filterdata" />
        </button>
      );
    } else {
      toRender = (
        <div>
          <button
            className="narrow_btnCloseFilters"
            onClick={() => {
              this.setState({ isOpen: false });
            }}
          >
            <FormattedMessage id="vx.filters.close" />
          </button>
          {/*this.props.children*/}

          <VXFilterContainerCollapsible titleLabel="Date range" isOpen={false} noScroll={true}>
            <Lastnobs />
            <div style={this.props.lastnobs_active ? { opacity: 0.4, pointerEvents: 'none' } : {}}>
              <FilterPeriod isNarrow={this.props.isNarrow} isRtl={this.props.isRtl} />
            </div>
          </VXFilterContainerCollapsible>

          <VXFilterContainerCollapsible titleLabel="Disaggregations" isOpen={false} noScroll={true}>
            <FilterSelection isNarrow={this.props.isNarrow} isRtl={this.props.isRtl} />
            <Filters isNarrow={this.props.isNarrow} isRtl={this.props.isRtl} />
          </VXFilterContainerCollapsible>
          <VXFilterContainerCollapsible
            titleLabel="Explore other datasets"
            isOpen={false}
            noScroll={true}
          >
            {showDataflowNavigator == 1 && (
              <DataflowNavigator
                stickyDims={getStickyDims}
                dataflowsOnEvidence={getDataflowsOnEvidence}
              />
            )}
          </VXFilterContainerCollapsible>
        </div>
      );
    }
    return <div className="NarrowFilters">{toRender}</div>;
  }
}

NarrowFilters.propTypes = {
  children: PropTypes.array,
  isNarrow: PropTypes.bool,
  isRtl: PropTypes.bool,
  lastnobs_active: PropTypes.bool,
};

const withProps = state => {
  return {};
};

function mapDispatchToProps(dispatch, props) {
  return {};
}

export default connect(withProps, mapDispatchToProps)(NarrowFilters);
