import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Tree } from '@blueprintjs/core';
import { getSelection } from '../../selectors/sdmx';

import { VXFilterContainerCollapsible } from '@sis-cc/dotstatsuite-ui-components';

//TODO:
//REMOVE HierarchicalCodelist (version1)
//Make the dimension to attach the hierarchy to a param.
//Disable the codes not in the codelist

class HierarchicalCodelist extends React.Component {
  constructor(props) {
    super(props);
    this.state = { expandedNodes: [], isOpen: false };
  }

  /*This doesn't get called... why?!?*/
  onChangeIsOpen = () => {
    this.setState({ isOpen: !this.state.isOpen });
  };

  handleNodeExpand = nodeData => {
    nodeData.isExpanded = true;
    this.setState({ expandedNodes: this.state.expandedNodes.concat(nodeData.id) });
  };
  handleNodeCollapse = nodeData => {
    nodeData.isExpanded = false;

    //this.setState(this.state);
    const newExpanded = [...this.state.expandedNodes];
    const idx = newExpanded.indexOf(nodeData.id);
    if (idx >= 0) newExpanded.splice(idx, 1);
    this.setState({ expandedNodes: newExpanded });
  };

  handleNodeClick = nodeData => {
    this.props.changeSelection('REF_AREA', nodeData.codeID, this.props.dimensions);
  };

  expandNodes = (node, toExpand) => {
    if (toExpand.includes(node.id)) {
      node.isExpanded = true;
    }
    if (node.childNodes && node.childNodes.length > 0) {
      node.childNodes.forEach(n => {
        this.expandNodes(n, toExpand);
      });
    }
  };

  render() {
    //Expand nodes
    if (!this.props.hierarchy) return null;
    if (this.props.hierarchy) {
      this.props.hierarchy.forEach(n => {
        this.expandNodes(n, this.state.expandedNodes);
      });
    }

    const hierarchyName = this.props.hierarchyName;
    if (hierarchyName == null) return null;

    if (this.props.isCollapsible) {
      return (
        <div className="TableConfig_wrapper">
          <VXFilterContainerCollapsible
            titleLabel={hierarchyName}
            isOpen={this.state.isOpen}
            noScroll={true}
            onChangeIsOpen={this.onChangeIsOpen}
          >
            <Tree
              className="hierarchy_tree"
              contents={this.props.hierarchy}
              onNodeExpand={this.handleNodeExpand}
              onNodeCollapse={this.handleNodeCollapse}
              onNodeClick={this.handleNodeClick}
            />
          </VXFilterContainerCollapsible>
        </div>
      );
    } else {
      return (
        <div className="TableConfig_wrapper">
          {!this.props.isCollapsible && hierarchyName != null && <h6>{hierarchyName}</h6>}
          <Tree
            contents={this.props.hierarchy}
            onNodeExpand={this.handleNodeExpand}
            onNodeCollapse={this.handleNodeCollapse}
            onNodeClick={this.handleNodeClick}
          />
        </div>
      );
    }
  }
}

const createTreeNode = (hierarchyNode, selCodes, codesInCodelist, depth = 0) => {
  let isSelCode = false;
  let selCodeParent = false;
  let selCodeCount = 0;
  let inCodelist = false;
  if (selCodes && selCodes.length > 0 && selCodes.includes(hierarchyNode.codeID)) {
    isSelCode = true;
    selCodeParent = true;
    selCodeCount++;
  }
  if (
    codesInCodelist &&
    codesInCodelist.length > 0 &&
    codesInCodelist.includes(hierarchyNode.codeID)
  ) {
    inCodelist = true;
  }

  let childNodes = null;
  if (hierarchyNode.codes && hierarchyNode.codes.length > 0) {
    childNodes = [];
    hierarchyNode.codes.forEach(c => {
      const ch = createTreeNode(c, selCodes, depth + 1);
      childNodes.push(ch);
      selCodeParent = selCodeParent || ch.selCodeParent;
      selCodeCount = selCodeCount + ch.selCodeCount;
    });
  }

  const ret = {
    id: hierarchyNode.id,
    codeID: hierarchyNode.codeID,
    label: hierarchyNode.name,
    selCodeParent: selCodeParent,
    selCodeCount: selCodeCount,

    depth: depth,
  };

  if (selCodeParent && !isSelCode) {
    ret.secondaryLabel = <span className="hier_selChildCount">{selCodeCount}</span>;
  }
  if (isSelCode) {
    ret.className = 'hier_sel';
  } else {
    ret.className = 'hier_unsel';
  }
  if (childNodes && childNodes.length > 0) {
    ret['childNodes'] = childNodes;
  }

  return ret;
};

const createTreeContents = (hierarchy, selItems, dims) => {
  if (!hierarchy || !hierarchy.hierarchies) return [];

  //MAKE dimIndex (INDEX) A PARAM
  const dimIndex = 0;
  const selCodes = [];

  if (selItems && selItems.length > 0) {
    selItems[dimIndex].values.forEach(v => {
      selCodes.push(v.id);
    });
  }

  const codesInCodelist = [];
  if (dims[dimIndex]) {
    dims[dimIndex].values.forEach(v => {
      codesInCodelist.push(v.id);
    });
  }

  const ret = [];
  hierarchy.hierarchies.forEach(hi => {
    ret.push(createTreeNode(hi, selCodes, codesInCodelist));
  });

  return ret;
};

HierarchicalCodelist.propTypes = {
  hierarchy: PropTypes.array,
  hierarchyName: PropTypes.string,
  dimensions: PropTypes.array,
  changeSelection: PropTypes.func,
  selItems: PropTypes.array,
  isCollapsible: PropTypes.bool,
};

const mapStateToProps = (state, props) => {
  return {
    hierarchy: createTreeContents(state.sdmx.hierarchy, getSelection(state), state.sdmx.dimensions),
    hierarchyName: state.sdmx.hierarchy ? state.sdmx.hierarchy.name : null,
    dimensions: state.sdmx.dimensions,
    selItems: getSelection(state),
  };
};

function mapDispatchToProps(dispatch, props) {
  //return bindActionCreators({incrementValue: incrementValue}, dispatch);
  return {
    changeSelection: (filterId, valueId, dims) =>
      dispatch({
        type: '@@vis/sdmx/CHANGE_DATAQUERY',
        payload: { filterId, valueId, dimensions: dims },
      }),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(HierarchicalCodelist);
