import React from 'react';
import * as R from 'ramda';
import { rules } from '@sis-cc/dotstatsuite-components';
import numeral from 'numeral';
import { DETableCell } from '@sis-cc/dotstatsuite-ui-components';

export const renameKeys = R.curry((keysMap, obj) =>
  R.reduce((acc, key) => R.assoc(keysMap[key] || key, obj[key], acc), {}, R.keys(obj)),
);

export const isValidNumber = R.both(R.is(Number), R.complement(R.equals(NaN)));
export const withIndex = R.addIndex(R.map)((value, index) => R.assoc('index', index)(value));

export const observationGetter = ({
  observations,
  dimensions,
  customAttributes,
  isRtl,
}) => () => coordinates => {
  const isUncoded = R.anyPass([
    R.prop('isFootnote'),
    () => R.equals(R.prop('name'), R.prop('id')),
    R.pipe(R.prop('id'), R.length, R.lte(4)),
  ]);

  const cellProps = isRtl => (value, valueFlags) => {
    const [uncodedFlags, codedFlags] = R.partition(isUncoded)(valueFlags);
    return { isRtl, value, codedFlags, uncodedFlags };
  };

  const getValueId = valueIndexes =>
    R.pipe(
      R.converge(R.prop, [R.pipe(R.prop('index'), R.nth(R.__, valueIndexes)), R.prop('values')]),
      R.prop('id'),
    );

  const formatDecimal = ({ customAttributes, valueIndexes }) => {
    const attr = R.prop('decimals', customAttributes);

    if (R.isNil(attr)) return R.identity;

    const d = Number(getValueId(valueIndexes)(attr));
    if (R.complement(isValidNumber)(d)) return R.identity;
    const format = R.equals(d, 0) ? '0' : `0,0.[${R.join('', R.times(R.always('0'), d))}]`;

    return value => numeral(value).format(format);
  };

  const formatScale = ({ customAttributes, valueIndexes }) => {
    const attr = R.prop('prefscale', customAttributes);
    if (R.isNil(attr)) return R.identity;

    const e = Number(getValueId(valueIndexes)(attr));
    if (R.complement(isValidNumber)(e)) return R.identity;

    return value => numeral(value).multiply(Math.pow(10, -1 * e));
  };

  const formatSeparator = value => {
    if (isValidNumber(value)) return numeral(value).format(`0,0.[0000000]`);
    return R.identity(value);
  };

  const getValue = customAttributes => values => {
    const valueIndexes = R.tail(values);

    return R.ifElse(
      isValidNumber,
      R.pipe(
        formatScale({ customAttributes, valueIndexes }),
        formatDecimal({ customAttributes, valueIndexes }),
        formatSeparator,
      ),
      R.always('-'),
    )(R.nth(0)(values));
  };

  const getValueFlags = attributeFlags =>
    R.addIndex(R.reduce)((acc, valueIndex, attributeIndex) => {
      if (R.isNil(valueIndex)) return acc;
      const flag = R.prop(attributeIndex)(attributeFlags);
      if (R.isNil(flag)) return acc;
      return R.append(
        R.pipe(
          R.prop('values'),
          R.nth(valueIndex),
          R.assoc('isFootnote')(R.prop('isFootnote')(flag)),
        )(flag),
      )(acc);
    }, []);

  const observation = R.pipe(
    R.toPairs,
    R.sortBy(
      R.pipe(R.prop(0), R.flip(R.prop)({ ...dimensions.many, ...dimensions.one }), R.prop('index')),
    ),
    R.pluck(1),
    R.join(':'),
    R.flip(R.propOr([]))(observations),
    R.converge(cellProps(isRtl), [
      getValue(customAttributes),
      R.pipe(R.tail, getValueFlags(R.prop('flags')(customAttributes))),
    ]),
  )({
    ...coordinates,
    ...R.reduce((memo, { id }) => ({ ...memo, [id]: 0 }), {})(R.values(dimensions.one)),
  });

  return <DETableCell {...observation} />;
};

export const observationFormat = ({ isRtl }) => ({ observations, dimensions, customAttributes }) =>
  observationGetter({
    dimensions,
    observations,
    customAttributes,
    isRtl,
  })(R.identity);

export const dimensionFormats = {
  label: rules.dimensionValueDisplay('label'),
  code: rules.dimensionValueDisplay('code'),
  both: rules.dimensionValueDisplay('both'),
};

export const getDimensionGetters = intl =>
  R.reduce(
    (memo, key) => ({
      ...memo,
      [key]: intl.formatMessage({ id: `vx.config.display.${key}` }),
    }),
    {},
    R.keys(dimensionFormats),
  );

export const makeTree = items => {
  const hierarchy = R.reduce(
    (index, item) =>
      item.parent in index
        ? R.assoc(item.id, [], R.assoc(item.parent, R.append(item.id, index[item.parent]), index))
        : R.assoc(item.id, [], index),
    {},
    items,
  ); //=> E.g. {"1":[2],"2":[3],"3":[],"4":[]}
  const index = R.map(R.head, R.groupBy(R.prop('id'), items)); //=> E.g. {"!": <item1>, "2": <item2>, ...}
  const assoc = id => {
    const childNodes = id => R.map(makeNode, hierarchy[id]);
    return R.isEmpty(childNodes(id)) ? index[id] : R.assoc('childNodes', childNodes(id), index[id]);
  };
  const makeNode = id => R.dissoc('parent', assoc(id));
  return R.map(makeNode, R.pluck('id', R.filter(item => R.isNil(item.parent), items)));
};

export const computeHasSpotlight = values => {
  const LIMIT = 8;
  if (R.gt(R.length(values), LIMIT)) return true;

  const computeflatArray = (acc, item) => {
    if (item.childNodes) return R.reduce(computeflatArray, acc, item.childNodes);
    return [...acc, item.id];
  };

  return R.gt(R.length(R.reduce(computeflatArray, [], values)), LIMIT);
};
