import { createSelector } from 'reselect';
import * as R from 'ramda';
import Set from 'es6-set';
import { getDataquery as getVisDataquery, getDimensionFormat } from './vis';
import { getSdmxAttribute } from '../lib/settings';

//------------------------------------------------------------------------------------------------#0
const getSdmx = R.prop('sdmx');

//------------------------------------------------------------------------------------------------#1
export const getDimensions = createSelector(getSdmx, R.prop('dimensions'));

const keysLimit = 800;

const limitRows = data => {
  if (typeof data == 'undefined') {
    return data;
  }
  const keys = Object.keys(data.dataSets[0].observations);
  if (keys.length <= keysLimit) {
    return data;
  }
  const ret = R.clone(data);

  for (let i = keysLimit; i < keys.length; i++) delete ret.dataSets[0].observations[keys[i]];

  ret.dataSets[0].limited = {
    returnedKeyCount: keys.length,
    limitedTo: keysLimit,
  };

  return ret;
};

const limitedCount = data => {
  if (typeof data == 'undefined') {
    return {
      returnedKeyCount: 0,
      limitedTo: 0,
    };
  }
  const keys = Object.keys(data.dataSets[0].observations);
  return {
    returnedKeyCount: keys.length,
    limitedTo: keysLimit,
  };
};

export const getData = createSelector(getSdmx, R.prop('data'));

export const getDataLimited = createSelector(getSdmx, R.pipe(R.prop('data'), limitRows));
export const getDataLimitedCount = createSelector(getSdmx, R.pipe(R.prop('data'), limitedCount));

export const getLayout = createSelector(getSdmx, R.prop('layout'));

export const getDataflows = createSelector(getSdmx, R.prop('dataflows'));
export const getMap = createSelector(getSdmx, R.prop('map'));

//------------------------------------------------------------------------------------------------#2
export const getDataquery = createSelector(
  getVisDataquery,
  getDimensions,
  (dataquery, dimensions) => {
    // dataquery in state is priority 1
    if (R.not(R.anyPass([R.isEmpty, R.isNil])(dataquery))) return dataquery;

    // dataquery extracted from dimensions default selection is priority 2
    return R.pipe(
      R.map(
        R.pipe(
          R.prop('values'),
          R.filter(R.propEq('isDefaultSelected', true)),
          R.pluck('id'),
          R.join('+'),
        ),
      ),
      R.join('.'),
    )(dimensions);
  },
);

export const getCustomAttributes = createSelector(getData, getDimensionFormat, (data, format) => {
  const flagsSet = new Set(getSdmxAttribute('flags'));
  const attrs = R.pathOr([], ['structure', 'attributes', 'observation'])(data);
  const isFootnote = true;

  const customAttrs = R.addIndex(R.reduce)(
    (memo, { id, name, values, ...rattr }, index) => {
      const attr = { ...rattr, id, values, index, isFootnote, label: format({ id, name }) };

      // flags defined in SETTINGS are *forced* footnotes
      // footnote is annotation...
      if (flagsSet.has(id)) return R.set(R.lensPath(['flags', index]), attr, memo);

      // decimals is used to format the obs value
      // should not be considered as a footnote
      if (R.includes(id, getSdmxAttribute('decimals'))) return R.assoc('decimals', attr, memo);

      // real footnotes handling: if single valued then footnote
      if (R.pipe(R.length, R.equals(1))(values))
        return R.over(R.lensProp('footnotes'), R.append(attr), memo);

      // if multi valued then flag
      if (R.pipe(R.isEmpty, R.not)(values)) return R.set(R.lensPath(['flags', index]), attr, memo);

      return memo;
    },
    { flags: {} /* , decimals: undefined */, footnotes: [] },
  )(attrs);

  return customAttrs;
});

//------------------------------------------------------------------------------------------------#3
export const getFilters = createSelector(
  getDimensions,
  getDataquery,
  R.pipe(
    R.useWith(
      (filters, dataquery) =>
        R.addIndex(R.map)((filter, index) => {
          //A filter has been forced for this dimension, just not return it.
          if (filter.forced) return null;

          if (R.pipe(R.nth(index), R.isEmpty)(dataquery)) return filter;
          if (R.pipe(R.nth(index), R.isNil)(dataquery)) return filter;

          const valueIdsSet = new Set(R.pipe(R.nth(index), R.split('+'))(dataquery));

          return R.over(
            R.lensProp('values'),
            R.map(
              R.ifElse(({ id }) => valueIdsSet.has(id), R.assoc('isSelected', true), R.identity),
            ),
            filter,
          );
        }, filters),
      [R.identity, R.split('.')],
    ),
    R.filter(R.pipe(R.prop('values'), R.length, R.lt(1))),
  ),
);

//------------------------------------------------------------------------------------------------#4
export const getSelection = createSelector(
  getFilters,
  R.pipe(
    R.map(R.over(R.lensProp('values'), R.filter(R.propEq('isSelected', true)))),
    R.filter(R.pipe(R.prop('values'), R.length, R.flip(R.gt)(0))),
  ),
);
