import * as R from 'ramda';
import md5 from 'md5';

const isValidNumber = R.both(R.is(Number), R.complement(R.equals(NaN)));

export const getIsSelected = ({ index, id, annotations, selection }) => {
  if (R.or(R.isEmpty(selection), R.isNil(selection))) {
    return R.ifElse(R.isEmpty, R.F, R.pipe(R.find(R.propEq('type', 'DEFAULT')), Boolean))(
      annotations,
    );
  }
  return !!R.prop(md5(`${index}${id}`))(selection);
};

export const getSortedCodes = lang =>
  R.sortBy(
    R.pipe(
      R.propOr([], 'annotations'),
      R.find(R.propEq('type', 'ORDER')),
      R.prop('text'),
      R.propOr(0, lang),
      Number,
      R.ifElse(isValidNumber, R.identity, R.always(0)),
    ),
  );
