import React from 'react';
import PropTypes from 'prop-types';

function MapSubfilterSummary(props) {
  const selItem = props.item.values.find(item => {
    return item.id == props.selected;
  });

  return (
    <div className="mapSubfilterGroup">
      <div className="mapSubfilterLbl">{props.item.name}</div>
      <div className="mapSubfilterSel">{selItem.name}</div>
    </div>
  );
}

MapSubfilterSummary.propTypes = {
  selected: PropTypes.string,
  item: PropTypes.object,
  onValueChange: PropTypes.func,
};

export default MapSubfilterSummary;
