import L from 'leaflet';
import 'leaflet/dist/leaflet.css';
import PropTypes from 'prop-types';
import React from 'react';
import { connect } from 'react-redux';
import { CHANGE_MAP_FILTERS } from '../../ducks/sdmx';
import { getMapRefAreaDimId } from '../../lib/settings';
import MapFilterGroup from './MapFilterGroup';
import { getLatestDatapoints } from './MapHelpers';

const TIME_PERIOD_ID = 'TIME_PERIOD';

const map_styles = {
  poly_disabled: {
    weight: 1,
    fillOpacity: 0.1,
    color: '#000000',
  },
  poly_highlight: {
    weight: 3,
  },
  fillOpacity: 0.85,
  mapColors: [
    '#0297C950',
    '#0297C970',
    '#0297C990',
    '#0297C9B0',
    '#0297C9D0',
    '#0297C9F0',
    '#0297C9FF',
  ],
};

class Map extends React.Component {
  filteredVals = null;
  obs_val_count = 0;

  updateSel = newsel => {
    this.props.changeFilter({ id: newsel.id, val: newsel.value });
  };

  //Returns the value from the refarea and the list of selected codes
  getValue = (refarea, selection) => {
    let key = []; //the key to access the value in the sdmx json
    let ret = {};
    let code = '';
    //foreach dimension
    for (let i = 0; i < this.props.sdmx.structure.dimensions.observation.length; i++) {
      let d = this.props.sdmx.structure.dimensions.observation[i];
      if (d.id == this.props.ref_area_dim_id) {
        //is the geo dimension? Assign it
        code = refarea;
        //Alway get the last datapoint: it has "_" as TIME_PERIOD_COORD
      } else if (d.id == TIME_PERIOD_ID) {
        code = '_';
      } else {
        //It is not the geo dimension get the selected code
        code = selection[d.id];
      }

      //get the index of the code
      if (code == '_') {
        key.push('_');
      } else {
        let idx = d.values.findIndex(i => i.id == code);
        if (idx == -1) return null;
        //assign name/label to the return obj
        ret[d.id] = { name: d.values[idx].name, id: d.values[idx].id };
        key.push(idx); //continue to compose the SDMX value access key
      }
    }

    //Access the value and assign it to the ret obj
    key = key.join(':');
    if (this.props.latest_datapoints[key]) {
      ret['obs_val'] = this.props.latest_datapoints[key];
      ret['attr'] = [];
      //Start from 1:the first one is the obs value. Stop at len-1: the last one is the time period
      for (let a = 1; a < ret['obs_val'].length - 1; a++) {
        if (ret['obs_val'][a] != null && !isNaN(ret['obs_val'][a])) {
          ret['attr'].push({
            n: this.props.sdmx.structure.attributes.observation[a - 1].name,
            v: this.props.sdmx.structure.attributes.observation[a - 1].values[ret['obs_val'][a]]
              .name,
          });
        }
      }
    } else ret['obs_val'] = null;

    return ret;
  };

  //Get all the values for the selected geo areas and the unique selection of the other dims
  getFilteredVals = () => {
    if (typeof this.props == 'undefined' || !this.props) return null;
    if (typeof this.props.sdmx == 'undefined' || !this.props.sdmx) return null;

    let vals = [];
    let geoDim = this.props.sdmx.structure.dimensions.observation.find(
      i => i.id == this.props.ref_area_dim_id,
    );
    geoDim.values.forEach(v => {
      vals.push(this.getValue(v.id, this.props.selection));
    });
    return vals;
  };

  onEachLayer = layer => {
    //search the value associated to the map shape
    let val = null;
    for (let i = 0; i < this.filteredVals.length; i++) {
      if (
        this.filteredVals[i][this.props.ref_area_dim_id]['id'] == layer.feature.properties.ISO3_CODE
      ) {
        val = this.filteredVals[i];
        break;
      }
    }

    //The calculated_style attrib is used to reassign the values after the on_mouse_over
    if (val && val.obs_val) {
      let scaledOpacity = -1;
      if (!isNaN(val.obs_val[0])) {
        scaledOpacity = this.getColorBucket(val.obs_val[0]);
      }

      const oldStyle = {
        weight: 1,
        fillOpacity: map_styles.fillOpacity,
        color: map_styles.mapColors[scaledOpacity],
      };
      layer.setStyle(oldStyle);

      layer.feature.properties['calculated_style'] = oldStyle;
    } else {
      layer.setStyle(map_styles.poly_disabled);
      layer.feature.properties['calculated_style'] = map_styles.poly_disabled;
    }

    layer.feature.properties['values'] = val;
  };

  highlighFeature = e => {
    let layer = e.target;
    layer.setStyle(map_styles.poly_highlight);
    if (!L.Browser.ie && !L.Browser.opera && !L.Browser.edge) {
      layer.bringToFront();
    }

    this.infobox.update(layer.feature.properties);
  };

  resetHighlight = e => {
    let layer = e.target;
    if (layer.feature.properties.calculated_style) {
      layer.setStyle(layer.feature.properties.calculated_style);
    }
    this.infobox.update();
  };

  zoomToFeature = e => {
    this.map.fitBounds(e.target.getBounds());
  };

  onEachFeature = (feature, layer) => {
    layer.on('mouseover', this.highlighFeature);
    layer.on('mouseout', this.resetHighlight);
    layer.on('click', this.zoomToFeature);
  };

  getColorBucket = val => {
    for (let i = 0; i < this.steps.length - 1; i++) {
      if (val >= this.steps[i] && val < this.steps[i + 1]) return i;
    }
    if (val == this.steps[this.steps.length - 1]) return this.steps.length - 2;
    return null;
  };

  _createLegendHTML = () => {
    let ret = '';
    let step1 = 0;
    let step2 = 0;
    for (let i = 0; i < this.steps.length - 1; i++) {
      step1 = Math.round(this.steps[i] * 10) / 10;
      step2 = Math.round(this.steps[i + 1] * 10) / 10;
      ret +=
        '<i style="background:' +
        map_styles.mapColors[i] +
        '"></i> ' +
        step1 +
        '&ndash;' +
        step2 +
        '<br>';
    }
    return ret;
  };

  createLegend = map => {
    const div = L.DomUtil.create('div', 'legend');
    div.innerHTML = this._createLegendHTML(1, 1);

    return div;
  };

  componentDidMount() {
    this.map = L.map('de_map', {
      center: [0, 0],
      attributionControl: false,
    });
    this.countryLayer = L.geoJSON(this.props.shape, {
      style: map_styles.poly_disabled,
      onEachFeature: this.onEachFeature,
    }).addTo(this.map);

    this.infobox = L.control();

    this.legend = L.control({ position: 'bottomright' });
    this.legend.onAdd = this.createLegend;

    this.infobox.onAdd = function(map) {
      this._div = L.DomUtil.create('div', 'map_info');
      this.update();
      return this._div;
    };

    this.infobox.update = function(props) {
      if (!props) {
        this._div.innerHTML = '<b>Hover on any active area</b>';
        return;
      }
      let val = '';
      let attribs = '';

      if (props.values && props.values.obs_val) {
        const dim_sel = [];
        for (let k in props.values) {
          if (props.values.hasOwnProperty(k) && k != props.ref_area_dim_id && k != 'obs_val') {
            dim_sel.push(props.values[k].name);
          }
        }
        val = props.values.obs_val[0];
        props.values.attr.forEach(a => {
          attribs = attribs + '<span class="attr_lbl">' + a.n + ': </span>' + a.v + '<br>';
        });

        this._div.innerHTML =
          '<b>' +
          props.NAME_ENGL +
          '</b>' +
          '<div id="map_time_period_val">' +
          'Time period: ' +
          props.values.obs_val[props.values.obs_val.length - 1] +
          '</div>' +
          /*'<div id="map_vars">' +
          dim_sel.join(', ') +
          '</div>' +*/
          '<div id="map_val">' +
          val +
          '</div>' +
          '<div id="map_attribs">' +
          attribs +
          '</div>';
      } else {
        this._div.innerHTML =
          '<b>' +
          props.NAME_ENGL +
          '</b>' +
          '<div id="map_time">Change your filters to get the value</div>' +
          '<div id="map_val"></div>';
      }
    };

    this.infobox.addTo(this.map);

    this.map.fitBounds(this.countryLayer.getBounds());

    this.countryLayer.eachLayer(this.onEachLayer);

    this.map.invalidateSize();
  }

  render() {
    this.valMin = Number.MAX_VALUE;
    this.valMax = Number.MIN_VALUE;
    this.valRange = this.valMax;
    this.obs_val_count = 0;
    this.steps = [];

    //Calculate the max/min to scale the colors
    if (typeof this.props.sdmx != 'undefined') {
      this.filteredVals = this.getFilteredVals();
      this.filteredVals.forEach(v => {
        if (v.obs_val) this.obs_val_count += 1;
      });
      this.filteredVals.forEach(v => {
        if (v.obs_val && !isNaN(v.obs_val[0])) {
          let toCompare = parseFloat(v.obs_val[0]);

          if (toCompare < this.valMin) {
            this.valMin = toCompare;
          }
          if (toCompare > this.valMax) {
            this.valMax = toCompare;
          }
        }
      });

      this.valRange = this.valMax - this.valMin;

      let step = this.valRange / map_styles.mapColors.length;
      for (let i = 0; i < map_styles.mapColors.length; i++) {
        this.steps.push(this.valMin + step * i);
      }
      this.steps.push(this.valMax);
    }

    if (typeof this.map != 'undefined' && typeof this.props.sdmx != 'undefined') {
      this.countryLayer.eachLayer(this.onEachLayer);

      this.legend.addTo(this.map);

      if (this.obs_val_count == 0) {
        this.infobox.update({ show: false });
      }
    }

    return (
      <React.Fragment>
        <MapFilterGroup
          filters={this.props.filters}
          selection={this.props.selection}
          onValueChange={this.updateSel}
        />

        <div id="de_map_container" style={{ position: 'relative', height: '500px' }}>
          <div id="de_map" />
          <div
            id="de_map_message"
            style={this.obs_val_count == 0 ? { display: '' } : { display: 'none' }}
          >
            <h5>Change your selection</h5>
            <div>
              No values available for the current selection, please change your filters combination
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

const mapStateToProps = state => {
  let refAreaDimId = getMapRefAreaDimId();
  let filters = [];

  if (typeof state.sdmx != 'undefined') {
    for (let i = 0; i < state.sdmx.data.structure.dimensions.observation.length; i++) {
      let d = state.sdmx.data.structure.dimensions.observation[i];
      //the filters: all the dimensions excluding the geo one and the time one
      if (d.id != refAreaDimId && d.id != TIME_PERIOD_ID) {
        filters.push(d);
      }
    }
  }

  //Get an alternate block of data points:
  //only keep the latest data point for any combination of the other dimensions.
  const latest = getLatestDatapoints(state.sdmx.data);

  return {
    shape: state.sdmx.map,
    sdmx: state.sdmx.data,
    latest_datapoints: latest,
    filters: filters,
    selection: state.sdmx.map_selection,
    ref_area_dim_id: refAreaDimId,
  };
};

function mapDispatchToProps(dispatch, props) {
  return {
    changeFilter: filter =>
      dispatch({
        type: CHANGE_MAP_FILTERS,
        payload: { filter },
      }),
  };
}

Map.propTypes = {
  shape: PropTypes.object,
  sdmx: PropTypes.object,
  latest_datapoints: PropTypes.object,
  filters: PropTypes.array,
  selection: PropTypes.object,
  changeFilter: PropTypes.func,
  ref_area_dim_id: PropTypes.string,
};

export default connect(mapStateToProps, mapDispatchToProps)(Map);
